import {createSlice, PayloadAction} from '@reduxjs/toolkit';
import {registration} from './registrationThunk';
import {RegistrationFormData1, RegistrationFormData2} from "../../api/type/registration";

const registrationSlice = createSlice({
    name: 'registration',
    initialState: {
        secret: '',
        email: '',
        password: '',
        country: '',
        referral: 1,
        error: false,
        isRegistered: false,
        loading: false,

        errorMessage: '',
        firstName: '',
        lastName: '',
        phone: '',
        id: 0,
        codeSms: '',
    },
    reducers: {
        onChangeForm1: (state, action: PayloadAction<RegistrationFormData1>) => {
            state.lastName = action.payload.lastName
            state.firstName = action.payload.lastName
            state.referral = action.payload.referral
        },
        onChangeForm2: (state, action: PayloadAction<RegistrationFormData2>) => {
            state.country = action.payload.country
            state.email = action.payload.email
            state.phone = action.payload.phone
        },
        onChangeForm3: (state, action: PayloadAction<{ code: string }>) => {
            state.codeSms = action.payload.code
        },
        onChangeForm4: (state, action: PayloadAction<{ password: string }>) => {
            state.password = action.payload.password
        },
    },
    extraReducers: (builder) => {
        builder.addCase(registration.pending, (state) => {
            state.loading = true;
        });
        builder.addCase(registration.fulfilled, (state, action) => {
            if (action.payload) {
                state.isRegistered = action.payload.success
                state.loading = false;
            }
        });
        builder.addCase(registration.rejected, (state) => {
            state.loading = false;
            state.error = true;
        });
    },

});
export const {onChangeForm1, onChangeForm2, onChangeForm3, onChangeForm4} = registrationSlice.actions;
export const registrationReducer = registrationSlice.reducer;