import {useForm} from "react-hook-form";
import {FormItem} from "../../../formItem/formItem";
import {Input} from "../../../../../../ui/inputs/input/input";
import {TakeOutButton} from "../../../../../../ui/buttons/TakeOutButton/TakeOutButton";
import {useSelector} from "react-redux";
import {AppStateType} from "../../../../../../core/redux/store";

interface Props {
  onSubmitHandle: (time: number) => void
}

export const SessionTimeOutForm = ({onSubmitHandle}: Props): JSX.Element => {
    const {sessionLiveTime} = useSelector((state: AppStateType) => state.user.user)
  const {handleSubmit, control} = useForm<{time: number}>({
    defaultValues: {
      time: sessionLiveTime
    },
    mode: 'onChange',
  });

  const onSubmit = ({time}: {time: number}) => {
    onSubmitHandle(time);
  }

  return (
      <form onSubmit={handleSubmit(onSubmit)}>
        <FormItem name='time' label='Время'>
          <Input control={control} name={'time'} rules={{required: true}}/>
        </FormItem>

        <TakeOutButton color={'blue'}>
          сохранить
        </TakeOutButton>
      </form>
  )
}