import {Layout} from "../../ui/layout/layout";
import { Route, Switch } from "react-router";
import { SmartComponent } from "./smartComponent/SmartComponent";
import { MainContractPage } from "./mainContractPage/MainContractPage";

export const Contacts = (): JSX.Element => {
  return (
    <Layout>
      <Switch>
        <Route exact path="/contacts" component={MainContractPage} />
        <Route path="/contacts/smart" component={SmartComponent} />
      </Switch>
    </Layout>
  )
}