import { createSlice } from '@reduxjs/toolkit';
import { axiosHeadersUpdater } from '../../../api/baseRequest';
import { eventsResponse } from '../../../api/type/events';
import { eventsThunk } from './eventsThunk';

const eventsSlice = createSlice({
    name: 'events',
    initialState: {
      events: {} as eventsResponse,
      loading: false,
    },
    reducers: {},
    extraReducers: (builder) => {
        builder.addCase(eventsThunk.pending, (state) => {
            state.loading = true;
            axiosHeadersUpdater()
        });
        builder.addCase(eventsThunk.fulfilled, (state, action) => {
            if (action.payload) {
                state.events = action.payload;
            }
        });
        builder.addCase(eventsThunk.rejected, (state) => {});
    },

});

export const eventsReducer = eventsSlice.reducer;