import styles from './chooseContract.module.scss';

import { ChooseContractSample } from './sample/chooseContractSample';

interface Props {
  items: Contract[];
}

export const ChooseContract = ({items}: Props): JSX.Element => {
  return (
    <div className={styles.contracts}>
      <h2>Мои контракты</h2>
      <div className={styles.panel}>
        <div className={styles.contractName}>
          Контракт
        </div>
        <div className={styles.amount}>
          Кол-во
        </div>
        <div className={styles.price}>
          Цена
        </div>
        <div className={styles.income}>
          Доход
        </div>
        <div className={styles.profitability}>
          Рентабельность
        </div>
      </div>

      {items && (
        <>
          <ChooseContractSample
          name='Разумный'
          amount={items.length}
          price={500 * items.length}
          income={200}
          profitability={items[0].percent}
          page='smart'
          >
          </ChooseContractSample>

          <ChooseContractSample
            name='Gold'
            amount={0}
            price={0}
            income={0}
            profitability={0}
            page='smart'
          >
          </ChooseContractSample>

          <ChooseContractSample
            name='Mango'
            amount={0}
            price={0}
            income={0}
            profitability={0}
            page='smart'
          >
          </ChooseContractSample>
        </>
      )}
    </div>
  )
}
