import { FC } from "react";
import styles from './info.module.scss';
import { ReactSVG } from 'react-svg';
import closeIcon from '../icons/close.svg';
import infoIcon from '../icons/info.svg';


interface Props {
  onClick: () => void
}

export const Info: FC<Props>  = ({ onClick }): JSX.Element => {
  return (
    <div className={styles.info}>
    <ReactSVG
      className={styles.closeIcon}
      src={closeIcon}
      onClick={() => onClick()}
    />
    <ReactSVG className={styles.mainInfoIcon} src={infoIcon} />
    <div className={styles.text}>
      Чтобы присоединиться, вам нужно купить контракт, выберите внизу, с какого контакта хотите начать
    </div>
    <button
      type='button'
      className={styles.button}
      onClick={() => onClick()}
    >
        Понятно
    </button>
  </div>
  )
}
