import {createAsyncThunk} from '@reduxjs/toolkit';
import {baseRequestAxios} from '../../../api/baseRequest';
import {GetInheritancesListResponse} from "../../../api/type/settings";

export const getInheritancesListThunk = createAsyncThunk(
    'getInheritancesList',
    async (_, {rejectWithValue}) => {
        try {
            const response = await baseRequestAxios.get<GetInheritancesListResponse>("/api/user/enter-heir")
            if (response.status === 200) {
                return response.data;
            }
        } catch (errors) {
            return rejectWithValue(errors)
        }
    }
)
