import { useEffect} from "react"
import { AppStateType } from '../../../core/redux/store';
import { useSelector, useDispatch } from 'react-redux';
import { getUserDataThunk } from '../../../modules/user/getUserData/getUserDataThunk';

import styles from './ReferralProfit.module.scss';
import { ReactSVG } from 'react-svg';
import ReferralProfitIcon from '../icons/referralProfit.svg';
import RZMBalanceIcon from '../icons/RZMBalance.svg';


export const ReferralProfit = (): JSX.Element => {
  const amountOfRewardsReceived = useSelector((state: AppStateType) => state.user.user?.amountOfRewardsReceived);
  const dispatch = useDispatch()

  useEffect(() => {
     dispatch(getUserDataThunk());
 }, [dispatch])

  return (
    <div className={styles.wrapper}>
      <div className={styles.ReferralProfit}>
      <div className={styles.iconBox}>
        <ReactSVG className={styles.ReferralProfitIcon} src={ReferralProfitIcon} />
      </div>
      <div className={styles.sum}>
        {amountOfRewardsReceived}
          <ReactSVG className={styles.RZMBalanceIcon} src={RZMBalanceIcon} />
      </div>
      <div className={styles.text}>
        Реферальные начисления
      </div>
      </div>
    </div>
  )
}
