import {createAsyncThunk} from '@reduxjs/toolkit';
import {baseRequestAxios} from '../../../api/baseRequest';
import {StructurePartners} from "../../../api/type/structure";

export const structurePartnersThunk = createAsyncThunk(
    'structurePartners',
    async (filter: {limit: number, offset : number}, {rejectWithValue}) => {
        try {
            const response = await baseRequestAxios.get<StructurePartners>("/api/structure/partners", {
                params: filter
            }
        )
            if (response.status === 200) {
                return response.data
            }
        } catch (errors) {
            return rejectWithValue(errors)
        }
    }
)