import {ReactSVG} from "react-svg";
import {useState} from "react";
import style from "./swichButton.module.css";

interface Props {
    icon: string,
    onChange?: (value: boolean) => void
}

export const SwitchButton = ({icon, onChange}: Props): JSX.Element => {
    const [active, setActive] = useState(false)

    const switchAction = () => {
        setActive(prev => !prev)

        if (onChange) {
            onChange(active)
        }
    }

    return (
        <div className={`${style.switcherWrapper} ${active ? style.active : ''}`}>
            <div className={style.button} onClick={switchAction}>
                <ReactSVG src={icon} className={`${style.icon}`}/>
            </div>
        </div>
    )
}
