export const Partners = (): JSX.Element => {
  const [isPartnerChosen, setIsPartnerChosen] = useState(false);

const getPartner = () => {
  setIsPartnerChosen(true);
}

    return (
        <Layout>
          {!isPartnerChosen && (
          <div className={styles.partnersPage}>
            <div className={styles.partnersList}>
            {fakePartners.map((partner: Partner) => (
              <PartnersCard onClick={getPartner} key={partner.id} image={partner.image} ruTitle={partner.ruTitle} ruText={partner.ruText} />
            ))}
            </div>
          </div>)}
          {isPartnerChosen && (<PartnersOpen />)}
        </Layout>
    )
}