import {createAsyncThunk} from '@reduxjs/toolkit';
import {baseRequestAxios} from '../../../api/baseRequest';

export const updateTimeSessionThunk = createAsyncThunk(
    'updateTimeSession',
    async ({time}: {time: number}, {rejectWithValue}) => {
        try {
            const response = await baseRequestAxios.post<{success: boolean}>("/api/user/live-time-update", {
                time: Number(time)
            })
            if (response.status === 200) {
                return response.data;
            }
        } catch (errors) {
            return rejectWithValue(errors)
        }
    }
)
