import {createAsyncThunk} from '@reduxjs/toolkit';
import {baseRequestAxios} from '../../api/baseRequest';
import {RegistrationRequest} from "../../api/type/registration";

export const registration = createAsyncThunk(
    'registration',
    async (requestData: RegistrationRequest, {rejectWithValue}) => {
        try {
            const response = await baseRequestAxios.post<{ success: boolean }>("/registration", {
                secret: requestData.secret,
                email: requestData.email,
                password: requestData.password,
                country: requestData.country,
                referral: requestData.referral
            })
            if (response.status === 200) {
                return response.data
            }
        } catch (errors) {
            return rejectWithValue(errors)
        }
    }
)

