import style from "../structure.module.css";
import {ButtonSettingsSchedule} from "../../../ui/buttons/buttonSettingsSchedule/buttonSettingsSchedule";
import iconScheduleSettings1 from "../../../assets/icons/iconScheduleSettings1.svg";
import iconScheduleSettings2 from "../../../assets/icons/iconScheduleSettings2.svg";
import iconScheduleSettings3 from "../../../assets/icons/iconScheduleSettings3.svg";
import iconScheduleSettings4 from "../../../assets/icons/iconScheduleSettings4.svg";
import iconCloseFilter from "../../../assets/icons/iconCloseFilter.svg";
import {useState} from "react";
import {ButtonFilterSchedule} from "../../../ui/buttons/buttonFilterSchedule/buttonFilterSchedule";
import {ReactSVG} from "react-svg";

export const ScheduleFilter = (): JSX.Element => {
    const [showFilter, setShowFilter] = useState(false)

    const toggleShowFilter = () => {
        setShowFilter(prev => !prev)
    }

    return (
        <div className={style.scheduleSettings}>
            <span className={style.title}>Статистика</span>
            <div className={`${showFilter ? style.filerShow : style.buttonsGroup}`}>
                {showFilter && (
                    <div className={style.filerShowBlock}>
                        <ButtonFilterSchedule onClick={toggleShowFilter}/>
                        <ReactSVG onClick={toggleShowFilter} className={iconCloseFilter} src={iconCloseFilter}/>
                    </div>
                )}

                <ButtonSettingsSchedule title={'По структуре'} icon={iconScheduleSettings1}/>
                <ButtonSettingsSchedule title={'По доходу'} icon={iconScheduleSettings2}/>
                <ButtonSettingsSchedule title={'По верификации'} icon={iconScheduleSettings3}/>
                <ButtonSettingsSchedule title={'По уровню'} icon={iconScheduleSettings4}/>
            </div>

            <ButtonFilterSchedule onClick={toggleShowFilter}/>
        </div>
    )
}