import { createAsyncThunk } from '@reduxjs/toolkit';
import { baseRequestAxios } from '../../api/baseRequest';
import { PromoListRequest, PromoListResponse } from '../../api/type/promo/promoList';

export const promoListThunk = createAsyncThunk(
  'promoList',
  async (requestData: PromoListRequest, {rejectWithValue}) => {
    try {
        const response  = await baseRequestAxios.get<PromoListResponse>("/promo/get", {
          params: requestData,
        })
        if (response.status === 200) {
            return response.data
        }
    } catch (errors){
        return  rejectWithValue(errors)
    }
  }
)
