import {useForm} from "react-hook-form";
import {useDispatch, useSelector} from "react-redux";
import {ReactSVG} from "react-svg";
import {ErrorsMessage} from "../../../../ui/errorsMessage/errorsMessage";
import {Input} from "../../../../ui/inputs/input/input";
import stranger from "../../../authorization/authorization.module.css";
import iconRepeatSmsSend from "../../../../assets/icons/iconRepeatSmsSend.svg";
import {AppStateType} from "../../../../core/redux/store";
import {RegistrationFormData3} from "../../../../api/type/registration";
import {onChangeForm3} from "../../../../modules/registration/registrationSlice";
import {checkSmsCode} from "../../../../modules/registration/checkSmsCode/checkSmsCodeThunk";
import style from "../../registration.module.css";
import {FormHeader} from "./formHeader";
import {FooterForm} from "./footerForm";
import {PropsForm} from "./registrationForms";

export const RegistrationForm3 = ({toggleActiveStep}: PropsForm): JSX.Element => {
    const {phone, errorMessage} = useSelector((state: AppStateType) => state.registration)
    const {errorCode} = useSelector((state: AppStateType) => state.checkSmsCode)
    const dispatch = useDispatch();

    const {handleSubmit, control, formState: {errors}} = useForm<RegistrationFormData3>({
        defaultValues: {
            smsCodeFirstNumber: '',
            smsCodeTwoNumber: '',
            smsCodeThreeNumber: '',
            smsCodeFourNumber: '',
        },
        mode: 'onChange',
    });

    const onSubmit = (data: RegistrationFormData3) => {
        const {smsCodeFirstNumber, smsCodeTwoNumber, smsCodeThreeNumber, smsCodeFourNumber} = data
        const code = `${smsCodeFirstNumber}${smsCodeTwoNumber}${smsCodeThreeNumber}${smsCodeFourNumber}`

        dispatch(onChangeForm3({code}))
        dispatch(checkSmsCode({phone, code: Number(code)}))

        toggleActiveStep(3)
    }

    return (
        <>
            <FormHeader title={'Подтверждение'} description={'Мы отправили вам сообщение на телефон'}>
                <span className={'phone'}> {phone} введите его ниже</span>
            </FormHeader>

            <button className={`${style.repeatSmsSend}`}>
                <ReactSVG src={iconRepeatSmsSend}/>Отправить еще раз
            </button>

            <form onSubmit={handleSubmit(onSubmit)}>
                <div className={`${stranger.formItem}`}>
                    {errorCode && <ErrorsMessage textMessage={'Неверный код'}/>}

                    <div className={`${style.inputsPhoneWrapper}`}>
                        <Input control={control} name='smsCodeFirstNumber'
                               rules={{required: true}}/>
                        <Input control={control} name='smsCodeTwoNumber'
                               rules={{required: true}}/>
                        <Input control={control} name='smsCodeThreeNumber'
                               rules={{required: true}}/>
                        <Input control={control} name='smsCodeFourNumber'
                               rules={{required: true}}/>
                    </div>
                    {errors && (<ErrorsMessage textMessage={'Эти поля обязательны'}/>)}
                    {errorCode && (<ErrorsMessage textMessage={'Неверный код'}/>)}
                    {errorMessage === 'To fast' && (
                        <ErrorsMessage textMessage={'Пожалуйста повоторите операцию через 5 минут'}/>)}
                    {errorCode && (<ErrorsMessage textMessage={'Глобальная ошибка'}/>)}
                </div>

                <FooterForm stepPrev={1} toggleActiveStep={toggleActiveStep}>
                    Продолжить
                </FooterForm>
            </form>
        </>
    )
}