import styles from './partnersCard.module.scss';
import MoreInfoButton from '../../../ui/buttons/BuyButton/BuyButton';
import {TakeOutButton} from '../../../ui/buttons/TakeOutButton/TakeOutButton';
import {ReactSVG} from "react-svg";
import linkIcon from '../icons/goToSite.svg';

interface Props {
    image: string;
    ruTitle: string;
    ruText: string;
    site?: string;
    onClick: () => void;
}

export const PartnersCard = (props: Props) => {
    const {image, ruTitle, ruText, onClick} = props;

    return (
        <div className={styles.item}>
            <div className={styles.image}>
                <img src={image} alt=""/>
            </div>
            <div className={styles.content}>
                <h2>{ruTitle}</h2>
                <p>{ruText}</p>
            </div>
            <div className={styles.buttons}>
                <div className={styles.infoButton}>
                    <MoreInfoButton onClick={() => onClick()} color='blue'>
                        Узнать больше
                    </MoreInfoButton>
                </div>
                <div className={styles.linkButton}>
                    <TakeOutButton onClick={onClick} color={'blue'}>
                        Перейти на сайт
                        <ReactSVG src={linkIcon} className={styles.linkIcon}/>
                    </TakeOutButton>
                </div>
            </div>
        </div>
    )
}
