import {useState} from 'react';
import styles from './NewsList.module.scss';
import {NewsItem} from '../NewsItem/NewsItem';
import {TakeOutButton} from '../../../../ui/buttons/TakeOutButton/TakeOutButton';

interface Props {
    onClick: any;
    articles: Article[];
    title: string;
    isButton: 'yes' | 'no';
}

export const NewsList = ({onClick, articles, title, isButton}: Props): JSX.Element => {
    const [limit, setLimit] = useState(4);
    const showArticles = articles.slice(0, limit)
    const upLimit = () => {
        setLimit(limit + 4);
    }

    return (
        <>
            <h2 className={styles.title}>{title}</h2>
            <div className={styles.newsList}>
                {showArticles.map((article: Article) => (
                    <div className={styles.newsItem} key={article.id}>
                        <NewsItem onClick={onClick} article={article}/>
                    </div>
                ))}
            </div>

            <div className={`${styles[isButton]}`}>
                <TakeOutButton color='green' onClick={() => upLimit()}>
                    Больше
                </TakeOutButton>
            </div>
        </>
    )
}
