import React from 'react';
import classnames from 'classnames';
import ButtonToday from '../../../ui/buttons/BuyButton/BuyButton';

import { ReactSVG } from 'react-svg';
import arrowLeftIcon from '../icons/arrowLeft.svg';
import arrowRightIcon from '../icons/arrowRight.svg';

import * as calendar from './calendar';

import './index.scss';


const getMonthName = (number) => {
  switch (number) {
      case 0:
          return 'Январь';
      case 1:
          return 'Февраль';
      case 2:
          return 'Март';
      case 3:
          return 'Апрель';
      case 4:
          return 'Май';
      case 5:
          return 'Июнь';
      case 6:
          return 'Июль';
      case 7:
          return 'Август';
      case 8:
          return 'Сентябрь';
      case 9:
          return 'Октябрь';
      case 10:
          return 'Ноябрь';
      case 11:
          return 'Декабрь';
      default: return;
  }
}

export class CalendarComponent extends React.Component {
    static defaultProps = {
        date: new Date(),
        years: [2021, 2022],
        monthNames: ['Январь', 'Февраль', 'Март', 'Апрель', 'Май', 'Июнь', 'Июль', 'Август', 'Сентябрь', 'Октябрь', 'Ноябрь', 'Декабрь'],
        weekDayNames: ['Пн', 'Вт', 'Ср', 'Чт' , 'Пт', 'Сб', 'Вс'],
        onChange: Function.prototype,
    };

    state = {
        date: this.props.date,
        currentDate: new Date(),
        selectedDate: null,
    };

    get year() {
        return this.state.date.getFullYear();
    }

    get month() {
        return this.state.date.getMonth();
    }

    get day() {
        return this.state.date.getDate();
    }

    TodayDate = () => {
      const date = new Date();

      this.setState({ date });
    }

    handlePrevMonthButtonClick = () => {
        const date = new Date(this.year, this.month - 1);
        
        this.setState({ date });
    };

    handleNextMonthButtonClick = () => {
        const date = new Date(this.year, this.month + 1);
        
        this.setState({ date });
    };

    handleSelectChange = () => {
        const year = this.yearSelect.value;
        const month = this.monthSelect.value;

        const date = new Date(year, month);

        this.setState({ date });
    };

    handleDayClick = date => {
        this.setState({ selectedDate: date });
        
        this.props.onChange(date);
    };

    render() {
        const { weekDayNames } = this.props;
        const { currentDate, selectedDate } = this.state;
        const monthData = calendar.getMonthData(this.year, this.month);
        const list = this.props.items;
        const month = getMonthName(this.state.date.getMonth());
        const year = this.state.date.getFullYear();

        return (
          <div className="calendar">
            <header className="header">
              <div className="leftPanel">
                <div className='calendarTitle'>
                  Календарь
                </div>
                <div className='buttonToday'>
                  <ButtonToday onClick={this.TodayDate}><span>Сегодня</span></ButtonToday>
                </div>
              </div>
              <div className="RightPanel">
                <div className="dateInfo">
                  <div className="showMonth">{month}</div>
                  <div className="showYear">{year}</div>
                </div>
                <div className="navigationBlock">
                  <div className='arrowBlock' onClick={this.handlePrevMonthButtonClick}>
                    <ReactSVG className='arrowIcon' src={arrowLeftIcon} />
                  </div>
                  <div className='arrowBlock' onClick={this.handleNextMonthButtonClick}>
                    <ReactSVG className='arrowIcon' src={arrowRightIcon} />
                  </div>
                </div>
              </div>
            </header>
            <div className='wrapper'>
              <table>
                  <thead>
                      <tr>
                          {weekDayNames.map(name =>
                              <th key={name}>{name}</th>
                          )}
                      </tr>
                  </thead>

                  <tbody>
                      {monthData.map((week, index) =>
                          <tr key={index} className="week">
                              {week.map((date, index) => date ?
                                  <td
                                      key={index}
                                      className={classnames('day', {
                                          'today': calendar.areEqual(date, currentDate),
                                          'selected': calendar.areEqual(date, selectedDate)
                                      })}
                                      onClick={() => this.handleDayClick(date)}
                                  >
                                    <div className='todayNumber'>
                                      {date.getDate()}
                                    </div>
                                    <div className='desktopInfo'>
                                      {!!list && (list.filter(item => item.date.slice(0, 10) === date
                                        .toLocaleString()
                                        .slice(0, 10)
                                        .replace(/\./g, '-'))
                                        .map(item => (
                                          <div className='event' key={item.date}>
                                            <p className='time'>{item.date.slice(10)}</p>
                                            <p className='name'>{item.ruTitle.slice(0, 20)}...</p>
                                          </div>
                                        )))}
                                    </div>
                                    <div className='mobileInfo'>
                                      {!!list && (list.filter(item => item.date.slice(0, 10) === date
                                        .toLocaleString()
                                        .slice(0, 10)
                                        .replace(/\./g, '-'))
                                        .map(item => (
                                          <div className='mobileEvent' key={item.date}>
                                          </div>
                                        )))}
                                    </div>
                                  </td>
                                  :
                                  <td key={index} />
                              )}
                          </tr> 
                      )}
                  </tbody>
              </table>
            </div>
          </div>
        );
    }
}