import style from "./personalData.module.css";
import {useForm} from "react-hook-form";
import {Input} from "../../../../../ui/inputs/input/input";
import {SelectCountries} from "../../../../../ui/selectCountries/selectCountries";
import {FormItem} from "../../formItem/formItem";
import {PersonalDataFormProps} from "../../../../../api/type/user";
import {useSelector} from "react-redux";
import {AppStateType} from "../../../../../core/redux/store";
import {TakeOutButton} from "../../../../../ui/buttons/TakeOutButton/TakeOutButton";

interface Props {
    onSubmitHandle: (data: any) => void
}

export const PersonalDataForm = ({onSubmitHandle}: Props): JSX.Element => {
    const {country, phone, email} = useSelector((state: AppStateType) => state.user.user)
    const {handleSubmit, control} = useForm<PersonalDataFormProps>({
        defaultValues: {
            country: country,
            phone: phone,
            email: email,
        },
        mode: 'onChange',
    });

    const onSubmit = (data: PersonalDataFormProps) => {
        onSubmitHandle(data);
    }

    const customStyles = {
        control: () => ({
            color: 'white',
            backgroundColor: 'transparent',
            display: 'flex',
            padding: '17px 0',
        }),
    }

    const onSelectCountries = (value: string) => {
        console.log(value)
    }

    return (
        <div className={style.personalDataForm}>
            <form onSubmit={handleSubmit(onSubmit)}>
                <div className={style.wrapperSelect}>
                    <label htmlFor="">Страна</label>
                    <div className={style.select}>
                        <SelectCountries onSelectCountries={onSelectCountries} customStyles={customStyles}/>
                    </div>
                </div>

                <FormItem name='email' label='Email'>
                    <Input control={control} name={'email'} rules={{required: true}}/>
                </FormItem>

                <FormItem label={'Телефон'} name='phone'>
                    <Input control={control} name={'phone'} rules={{required: true}}/>
                </FormItem>

                <TakeOutButton color={'blue'}>
                    Сохранить
                </TakeOutButton>
            </form>
        </div>
    )
}
