import styles from './HistoryList.module.scss'
import { ReactSVG } from 'react-svg';
import filterIcon from '../../contacts/icons/filterIcon.svg';
import ActiveFilterIcon from '../../contacts/icons/ActiveFilterIcon.svg';
import DNCCoin from '../../dashboard/icons/DNCBalance.svg';
import MoreInfoIcon from '../icons/moreInformation.svg';
import FilterIcon from '../icons/filter.svg';
import closeIcon from '../icons/close.svg';
import { useEffect, useState } from "react";
import { Pagination } from '../../../ui/pagination/pagination';

interface Props {
  items: Transaction[];
  count: number
}

export const HistoryList = ({items}: Props) => {
  let goodsList = items.slice();
  const [sortBy, setSortBy] = useState('');
  const [selectedContracts, setSelectedContracts] = useState(0)
  const [filter, setFilter] = useState('all');
  const [isReverse, setIsReverse] = useState(false);
  const [isMobileFilter, setIsMobileFilter] = useState(false);
  
  useEffect(() => {
}, [])

  const changeFilter = (type: string) => {
    setFilter(type);
  }

  const handleMobileFilter = () => {
    setIsMobileFilter(!isMobileFilter);
  }

  const getFilteredList = (type: string) => {
    switch (type) {
      case 'contract':
        return goodsList.filter(item => (item.type === 2 || item.type === 5 || item.type === 17));

      case 'refill':
        return goodsList.filter(item => item.type === 0);

      case 'withdrawal':
        return goodsList.filter(item => item.type === 1);

      case 'structure':
        return goodsList.filter(item => (item.type === 3 || item.type === 4 || item.type === 7 || item.type === 8));

      case 'all':
        return goodsList;

      default:
        return goodsList;
    }
  }

const setSortType = (type: string) => {
  if (sortBy === type) {
    setIsReverse(!isReverse);
  }

  setSortBy(type);
}

const changeMobileFilter = (type: string) => {
  setSortType(type);
  handleMobileFilter();
}

const checkStatus = (list: Transaction[]) => {
  if (sortBy) {
    
    list.sort((first, second) => {
      switch (sortBy) {
        case 'id':
          return first.id - second.id;

        case 'amount':
          return first.amount - second.amount;

        case 'type':
          return first.type - second.type;

        case 'status':
          
          return first.status - second.status;

        case 'date':
          return second.dateCreate.localeCompare(first.dateCreate);

        default:
          return 0;
      }
    });
  }
};

checkStatus(goodsList);

if (isReverse) {
  goodsList.reverse();
}

const handlePageClick = (selected: number) => {
  setSelectedContracts(selected);
}

const showAmountOfContracts = (number: number) => {
  const start = number * 10;
  const end = start + 10;

  return goodsList.slice(start, end);
}

  goodsList = getFilteredList(filter);
  return (
    <>
      <div className={styles.filterPanel}>
        <h2 className={styles.title}>История</h2>
        <ul className={styles.filterList}>
          <li
            className={`${styles.filterItem} ${filter === 'all'? styles.activeItem: ''}`}
            onClick={() => changeFilter('all')}
          >Все</li>

          <li 
            className={`${styles.filterItem} ${filter === 'contract'? styles.activeItem: ''}`}
            onClick={() => changeFilter('contract')}
          >Контракт</li>

          <li
            className={`${styles.filterItem} ${filter === 'refill'? styles.activeItem: ''}`}
            onClick={() => changeFilter('refill')}
          >Пополнение</li>

          <li
            className={`${styles.filterItem} ${filter === 'withdrawal'? styles.activeItem: ''}`}
            onClick={() => changeFilter('withdrawal')}
          >Вывод</li>

          <li
            className={`${styles.filterItem} ${filter === 'structure'? styles.activeItem: ''}`}
            onClick={() => changeFilter('structure')}
          >Структура</li>

        </ul>
        <div className={styles.filterName} onClick={() => handleMobileFilter()}>
          <ReactSVG className={styles.FilterIcon} src={FilterIcon}/>
          Фильтры
        </div>
      </div>
      <div className={styles.panel}>
        <div className={styles.sortByIndex} onClick={() => (setSortType('id'))}>
          Номер транзакции
          {(sortBy === 'id') && (<ReactSVG className={styles.ActiveFilterIcon} src={ActiveFilterIcon} />)}
          {(sortBy !== 'id') && (<ReactSVG className={styles.filterIcon} src={filterIcon} />)}
        </div>
        <div className={styles.sortByAmount} onClick={() => (setSortType('amount'))}>
          Сумма
          {(sortBy === 'amount') && (<ReactSVG className={styles.ActiveFilterIcon} src={ActiveFilterIcon} />)}
          {(sortBy !== 'amount') && (<ReactSVG className={styles.filterIcon} src={filterIcon} />)}
        </div>
        <div className={styles.sortByType} onClick={() => (setSortType('type'))}>
          Тип транзакции
          {(sortBy === 'status') && (<ReactSVG className={styles.ActiveFilterIcon} src={ActiveFilterIcon} />)}
          {(sortBy !== 'status') && (<ReactSVG className={styles.filterIcon} src={filterIcon} />)}
        </div>
        <div className={styles.sortByStatus} onClick={() => (setSortType('status'))}>
          Статус
          {(sortBy === 'type') && (<ReactSVG className={styles.ActiveFilterIcon} src={ActiveFilterIcon} />)}
          {(sortBy !== 'type') && (<ReactSVG className={styles.filterIcon} src={filterIcon} />)}
        </div>
        <div className={styles.sortByHistoryDate} onClick={() => (setSortType('date'))}>
          Дата
          {(sortBy === 'date') && (<ReactSVG className={styles.ActiveFilterIcon} src={ActiveFilterIcon} />)}
          {(sortBy !== 'date') && (<ReactSVG className={styles.filterIcon} src={filterIcon} />)}
        </div>
      </div>
      {isMobileFilter && (
        <div className={styles.mobilePanel}>
          <div className={styles.headerBlock}>
            <div className={styles.filterBlock}>
              <ReactSVG className={styles.filterIcon} src={FilterIcon} />
              Фильтр
            </div>
            <ReactSVG className={styles.closeIcon} src={closeIcon}  onClick={() => handleMobileFilter()}/>
          </div>
          <ul className={styles.mobileFilterList}>
            <li
              className={`${styles.mobileFilterItem} ${sortBy === 'date'? styles.mobileActiveItem: ''}`}
              onClick={() => (changeMobileFilter('date'))}
            >Дата</li>
            <li
              className={`${styles.mobileFilterItem} ${sortBy === 'type'? styles.mobileActiveItem: ''}`}
              onClick={() => (changeMobileFilter('type'))}
            >Тип транзакции</li>
            <li
              className={`${styles.mobileFilterItem} ${sortBy === 'status'? styles.mobileActiveItem: ''}`}
              onClick={() => (changeMobileFilter('status'))}
            >Статус</li>
            <li
              className={`${styles.mobileFilterItem} ${sortBy === 'amount'? styles.mobileActiveItem: ''}`}
              onClick={() => (changeMobileFilter('amount'))}
            >Сумма</li>
          </ul>
        </div>
      )}
      {goodsList.length
        ? (
          <>
          <ul className={styles.list}>
            {goodsList && ((showAmountOfContracts(selectedContracts)).map(item => (
              <TransactionItem item={item} key={item.id}/>
            ))) }
          </ul>
          <div className={styles.paginationBlock}>
              <Pagination pageCount={goodsList.length / 10} handlePageClick={handlePageClick}/>
          </div>
        </>
        )
      : (
        <p className={styles.errorText}>Транзакции отстутствуют</p>
      )}
  </>
  )
}

interface TransactionItemProps {
  item: Transaction;
}


export const TransactionItem = ({item}: TransactionItemProps) => {
  const { id, amount, dateCreate, status, type } = item;

  const getStatus = (number: number) => {
    switch (number) {
      case 0:
        return 'Ожидает';

      case 1:
        return 'Исполнена';

      case 2:
        return 'Отклонена ';

    }
  }


  const getType = (number: number) => {
    switch (number) {
      case 0:
        return 'Пополнение';

      case 1:
        return 'Вывод';

      case 2:
        return 'Доход';

      case 3:
        return 'Start Bonus';

      case 4:
        return 'Expert Bonus';

      case 5:
        return 'Покупка контракта';
      
      case 6:
        return 'Возврат средств';

      case 7:
        return 'Professional Bonus';

      case 8:
        return 'Super Bonus';

      case 9:
        return 'Покупка курса';

      case 10:
        return 'Оплата заказа';

      case 11:
        return 'Покупка в Mango.Store';

      case 12:
        return 'Возврат из Mango.Store';

      case 14:
        return 'Покупка IDO';

      case 15:
        return 'P2P покупка';

      case 16:
        return 'Возврат средст с IDO в DNC';

      case 31:
        return 'P2P продажа';

      case 17:
        return 'Продажа конракта';

      case 18:
        return 'Покупка членства в клубе';

      case 19:
        return 'Реферальный бонус клуба';

      case 20:
        return 'Покупка доступа на мероприятие';

      case 21:
        return 'Оплата комиссии за IDO';

      case 22:
        return 'Изменение заказа';

      case 24:
        return 'Покупка тикета';

      case 25:
        return 'Покупка тикета с временного баланса';

      case 26:
        return 'Перевод средств на временный баланс';

      case 40:
        return 'FAI депозит';

      case 41:
        return 'FAI снятие депозита';

      case 52:
        return 'Оплата комиссии с временного баланса';
        
      case 53:
        return 'Оплата комиссии';

      case 60:
        return 'Оплата билета на мероприятие';

      default:
        return 'Неизвестно';
      }

  }

  return (
    <li className={styles.item}>
      <div className={styles.firstPart}>
        <div className={styles.index}>
          № {id}
        </div>
        <div className={styles.MobileDate}>
          {dateCreate}
          <span className={styles.mobileText}>Дата</span>
        </div>
        <div className={styles.amount}>
          {amount}
        </div>
      </div>

      <div className={styles.secondPart}>
        <div className={styles.MobileAmount}>
          <div>
            {amount}
            <ReactSVG className={styles.coin} src={DNCCoin} />
          </div>
          <span className={styles.mobileText}>Сумма</span>
        </div>
        <div className={`${styles.dnc} ${styles.type}`}>
            <p className={styles.typeText}>{getType(type)}</p>
        </div>
        <div className={`${styles.status} ${styles.rzm}`}>
            {getStatus(status)}
        </div>
        <div className={styles.date}>
          {dateCreate}
        </div>
        <div className={styles.rejectContract}>
          <div className={styles.rejectBox} onClick={() => {}}>
            <ReactSVG className={styles.MoreInfoIcon} src={MoreInfoIcon} />
          </div>
        </div>
      </div>
    </li>
  )
}
