import {NavLink} from 'react-router-dom';
import {ReactSVG} from 'react-svg';
import styles from './NavBarOpen.module.scss';
import bigLogoIcon from '../icons/big-logo.svg';
import smallLogoIcon from '../icons/small-logo.svg';
import dashboardIcon from '../icons/dashboard.svg';
import contactsIcon from '../icons/contacts.svg';
import structureIcon from '../icons/structure.svg';
import calendarIcon from '../icons/calendar.svg';
import partnersIcon from '../icons/partners.svg';
import promoIcon from '../icons/promo.svg';
import historyOpenIcon from '../icons/history-hover.svg';
import historyIcon from '../icons/history.svg';

const menu = [
    {
        value: 'Дашборд',
        icon: dashboardIcon,
        src: '/dashboard'
    },
    {
        value: 'Контракты',
        icon: contactsIcon,
        src: '/contacts'
    },
    {
        value: 'Структура',
        icon: structureIcon,
        src: '/structure'
    },
    {
        value: 'Календарь',
        icon: calendarIcon,
        src: '/calendar'
    },
    {
        value: 'Партнеры',
        icon: partnersIcon,
        src: '/partners'
    },
    {
        value: 'Промо',
        icon: promoIcon,
        src: '/promo'
    }
]

export const NavBarOpen = () => {
    return (
        <nav className={styles.nav}>
            <div className={styles.logo}>
                <ReactSVG className={styles.bigLogo} src={bigLogoIcon} />
                <ReactSVG className={styles.smallLogo} src={smallLogoIcon} />
            </div>
            <ul className={styles.list}>
                {menu.map((item) =>
                    <NavBarItem key={item.value} value={item.value} src={item.src} icon={item.icon}/>
                )}

            </ul>
             <div className={styles.history}>
              <NavBarHistory src={'history'}/>
            </div>
        </nav>
    )
}

interface Props {
    icon: string,
    value?: string,
    src: string,
}

interface HistoryProps {
  src: string,
}

const NavBarHistory = ({src}: HistoryProps): JSX.Element => {
  return (
    <NavLink to={src}>
      <ReactSVG className={styles.historyOpenIcon} src={historyOpenIcon}/>
      <ReactSVG className={styles.historyIcon} src={historyIcon} />
    </NavLink>
  )
}

const NavBarItem = ({icon, value, src}: Props): JSX.Element => {
    return (
        <li className={styles.item}>
            <NavLink activeClassName={`${styles.active}`} to={src}>
                <ReactSVG className={styles.icon} src={icon}/>
                <span className={styles.text}>{value}</span>
            </NavLink>
        </li>
    )
}
