import {createAsyncThunk} from '@reduxjs/toolkit';
import {baseRequestAxios} from '../../../api/baseRequest';
import {UpdateUserPasswordRequest} from "../../../api/type/user";

export const updateUserFinPasswordThunk = createAsyncThunk(
    'updateUserPassword',
    async ({new_password, old_password}: UpdateUserPasswordRequest, {rejectWithValue}) => {
        try {
            const response = await baseRequestAxios.post<{success: boolean}>("/api/user/fin-password", {
                old_password: old_password,
                new_password: new_password
            })
            if (response.status === 200) {
                return response.data;
            }
        } catch (errors) {
            return rejectWithValue(errors)
        }
    }
)
