import { FC } from "react";
import styles from './chooseContractSample.module.scss';
import { NavLink } from 'react-router-dom';

import { ReactSVG } from 'react-svg';
import contractIcon from '../../icons/contract.svg';
import arrowIcon from '../../icons/arrow.svg';

interface Props {
  name: 'Разумный' | 'Gold' | 'Mango';
  amount: number;
  price: number;
  income: number;
  profitability: number;
  page: string;
}

export const ChooseContractSample: FC<Props> = (props): JSX.Element => {
  const { name, amount, price, income, profitability, page } = props;

  return (
    <>
      <div className={styles.contract}>
        <div className={styles.firstPart}>
          <div className={styles.contractName}>
            <ReactSVG className={`${styles.contractIcon} ${styles[name]}`} src={contractIcon} />
            <span>{name === 'Mango' ? `${name} Banking` : name}</span>
          </div>
          <div className={styles.amount}>
            <span>{amount}</span>
            <p className={styles.mobileText}>Кол-во</p>
          </div>
        </div>
        <div className={styles.secondPart}>
          <div className={styles.price}>
            <span>{price}</span>
            <p className={styles.mobileText}>Цена</p> 
          </div>
          <div className={styles.income}>
          <span>{income}</span>
           <p className={styles.mobileText}>Доход</p>
          </div>
          <div className={styles.profitability}>
            <span>{profitability}%</span>
            <p className={styles.mobileText}>Рентаб.</p>
          </div>
          <div className={styles.arrow}>
            <NavLink to={`/contacts/${page}`}>
              <div className={styles.arrowWrapper} >
                <ReactSVG className={styles.arrowIcon} src={arrowIcon} />
              </div>
            </NavLink>
            </div>
        </div>
      </div>
    </>
  )
}
