import {ReactSVG} from "react-svg";
import {DeleteContact} from "../../../../../../ui/buttons/deleteContact/deleteContact";
import style from '../inheritance.module.css'

interface InheritanceBlockProps {
    face: string;
    name: string;
    phone: string;
    email: string;
    id: number;
    deleteAction: (id: number) => void;
}

export const InheritanceList = ({face, email, name, phone, id, deleteAction}: InheritanceBlockProps): JSX.Element => {
    return (
        <div className={style.inheritanceList}>
            {face && (<ReactSVG src={face} className={style.face}/>)}
            <div className={style.contacts}>
                <span className={style.name}>{name}</span>

                <div className={style.contactsData}>
                    <span className={style.phone}>{phone}</span>
                    <span className={style.mail}>{email}</span>
                </div>
            </div>
            <DeleteContact onClick={() => deleteAction(id)}/>
        </div>
    )
}