import { createSlice } from '@reduxjs/toolkit';
import { axiosHeadersUpdater } from '../../../api/baseRequest';
import { buyContractsThunk } from './buyContractsThunk';

const buyContractsSlice = createSlice({
    name: 'buyContracts',
    initialState: {
      success: false as boolean | undefined,
      loading: false,
    },
    reducers: {},
    extraReducers: (builder) => {
        builder.addCase(buyContractsThunk.pending, (state) => {
            state.loading = true;
            axiosHeadersUpdater()
        });
        builder.addCase(buyContractsThunk.fulfilled, (state, action) => {
            if (action.payload?.success || action.payload?.status) {
                state.success = action.payload.status;
            }
        });
        builder.addCase(buyContractsThunk.rejected, (state) => {});
    },

});

export const buyContractReducer = buyContractsSlice.reducer;
