import Select from 'react-select';
import style from './select.module.css';
import {countries} from "./countries/countries";

interface OptionsLabel {
    value: string,
    label: { ru: string, en: string },
    src: string
}

interface SelectCountriesProps {
    customStyles: any,
    onSelectCountries: (value: string) => void
}

export const SelectCountries = ({customStyles, onSelectCountries}: SelectCountriesProps): JSX.Element => {

    const handleChange = ({value}: any) => {
        onSelectCountries(value)
    }

    return (
        <>
            <Select className={style.select}
                    options={countries}
                    components={{Option: CustomOption}}
                    onChange={handleChange}
                    styles={customStyles}
                    defaultValue={countries[0]}
                    formatOptionLabel={FormatOptionLabel}
            />
        </>
    )
}

const FormatOptionLabel = (props: OptionsLabel): JSX.Element => {
    return (
        <div className={style.selectedCountries}>
            <img className={style.flag} src={props.src} alt="flags"/>
            <span className={style.counter}>{props.label.ru}</span>
        </div>
    );
}


const CustomOption = ({innerRef, innerProps, ...rest}: any) => {
    return (
        <div ref={innerRef} {...innerProps} className={style.countries}>
            <img className={style.flag} src={rest.data.src} alt="flag"/>
            <span>{rest.label.ru}</span>
        </div>
    )
}
