import styles from './partnersOpen.module.scss';
import PartnerImage from './icons/exampleImage.png';
import {ReactSVG} from 'react-svg';
import partnersIcon from './icons/partners.svg';
import ladderIcon from './icons/ladder.svg';
import educationIcon from './icons/education.svg';
import flagIcon from './icons/flag.svg';

export const PartnersOpen = (): JSX.Element => {
  return (
        <div className={styles.PartnersOpenPage}>
          <div className={styles.imageBlock}>
            <div className={styles.imageWrapper}>
              <img src={PartnerImage} alt="PartnerImage" />
            </div>
            <div className={styles.contentBlock}>
              <p>
                Ассоциация Коммерческого Фандрайзинга
              </p>
              <span>
                АО "Фандрайзер номер один". Первая в России Фандрайзинговая организация.
              </span>
            </div>
          </div>
          <h3 className={styles.title}>
            Комьюнити людей, которые профессионально занимаются привлечением ресурсов, втом числе финансовых, в социально-значимые проекты и бизнес.
          </h3>
          <div className={styles.iconsBlock}>
            <div className={styles.iconsItem}>
              <ReactSVG className={styles.iconItem} src={partnersIcon}/>
              <div className={styles.contentBlock}>
                <p>
                  90+
                </p>
                <span>
                  участников
                </span>
              </div>
            </div>
            <div className={styles.iconsItem}>
              <ReactSVG className={styles.iconItem} src={ladderIcon}/>
              <div className={styles.contentBlock}>
                <p>
                  200+
                </p>
                <span>
                  бизнес-проектам помогли в развитии
                </span>
              </div>
            </div>
            <div className={styles.iconsItem}>
              <ReactSVG className={styles.iconItem} src={educationIcon}/>
              <div className={styles.contentBlock}>
                <p>
                  2021 г.
                </p>
                <span>
                  стартовала Школа Фандрайзеров России
                </span>
              </div>
            </div>
          </div>
          <h3 className={styles.title}>
            Эта школа была учреждена Ассоциацией Фандрайзеров России при поддержке платформы ManGo.
          </h3>
          <div className={styles.flagsBlock}>
            <div className={styles.flagsItem}>
              <ReactSVG className={styles.flagIcon} src={flagIcon}/>
              Получение студентами новой профессии, с возможностью заработков без "потолков"
            </div>
            <div className={styles.flagsItem}>
              <ReactSVG className={styles.flagIcon} src={flagIcon}/>
              Важнейшая информация, как правильно привлекать инвестиционные денежные средства            </div>
            <div className={styles.flagsItem}>
              <ReactSVG className={styles.flagIcon} src={flagIcon}/>
              Практический навык грамотно отбирать бизнес-предложения для работы;            </div>
          </div>
          <div className={styles.firstSpecialText}>
            Именно эти постулаты и принципы пропагандирует в своей деятельности Ассоциация Фандрайзеров России.
            За год существования Ассоциация привлекла более <span>2,5 млрд</span> российских рублей в социально значимые проекты, бизнес и предприятия.
          </div>
          <h3 className={styles.title}>
            Особенное внимание уделяется работе по развитию микро-, малого и среднего сектора.
          </h3>
          <div className={styles.secondSpecialText}>
            <p>
              Именно эти постулаты и принципы пропагандирует в своей деятельности Ассоциация Фандрайзеров России.
              За год существования Ассоциация привлекла более 2,5 млрд российских рублей в социально значимые проекты, бизнес и предприятия.
            </p>
            <p>
              В планах на ближайшее время - мощнейшее маштабирование в сотни раз. И кратное увеличение своего положительного влияния. Именно для таких амбиционых целей
              создана Школа Фандрайзеров России. Студенты, получившие образование в этой сфере, смогут не только повлиять на качество собственной жизни, но и стать частью большого, экономически значимого проекта.
            </p>
          </div>
          <div className={styles.lastBlock}>
            <div className={styles.textBlock}>
            Вы тоже можете стать частью сообщества фандрайзеров, освоить эту профессию и применять полученные знания во благо.
            </div>
            <div className={styles.textBlock}>
            Обучиться профессии фандрайзера можно, перейдя по этой ссылке:
            <br />
             <span>dribbble.com/search/shots/popular/web-design?q=news</span>
            </div>
          </div>
        </div>
  )
}