import {ReactSVG} from "react-svg";
import { FC } from "react";
import { ButtonEnter } from "../../../../ui/buttons/buttonEnter/buttonEnter";
import stranger from "../../../authorization/authorization.module.css";
import iconArrowBack from '../../../../assets/icons/iconArrowBack.svg';
import style from "../../registration.module.css";

interface Props {
    stepPrev: number,
    toggleActiveStep: (value: number) => void,
    disabled?: boolean
}

export const FooterForm: FC<Props> = ({toggleActiveStep, stepPrev, disabled, children}) => {
    return (
      <div className={`${stranger.footerForm} ${style.footerFormFlex}`}>
          <button className={style.backBtn} onClick={() => toggleActiveStep(stepPrev)}>
               <ReactSVG src={iconArrowBack} className={style.iconArrowBack} />
              Назад
          </button>
          <ButtonEnter disabled={disabled} value={children}/>
      </div>
  )
}