import { createAsyncThunk } from '@reduxjs/toolkit';
import { baseRequestAxios } from '../../../api/baseRequest';
import { ArticlesResponse } from "../../../api/type/articles";


export const articlesThunk = createAsyncThunk(
  'articles',
  async (_, {rejectWithValue}) => {
    try {
        const response = await baseRequestAxios.get<ArticlesResponse>("/article/list", {})
        if (response.status === 200) {
            return response.data;
        }
    } catch (errors){
        return rejectWithValue(errors)
    }
  }
)