import {useForm} from "react-hook-form";
import {useDispatch, useSelector} from "react-redux";
import {SwitchBlock} from "../../../../../../ui/switchBlock/switchBlock";
import {TakeOutButton} from "../../../../../../ui/buttons/TakeOutButton/TakeOutButton";
import {AppStateType} from "../../../../../../core/redux/store";
import {NotificationsFormEmailProps} from "../../../../../../api/type/user";
import {toggleNotificationsEmailForm} from "../../../../../../modules/user/getUserData/getUserDataSlice";
import style from '../notifications.module.css'
import {FormItem} from "../formItem";

interface Props {
    onSubmitHandle: (data: NotificationsFormEmailProps) => void
}

export const FormEmail = ({onSubmitHandle}: Props): JSX.Element => {
    const {onIncomeMail, onContractIncomeMail, onSuccessWithdrawMail, onStartBonusMail,
        onNewItemsInCalendarMail, onEventMail, onCourseMail, onWebinarsMail, onNewsMail
    } = useSelector((state: AppStateType) => state.user.user)

    const dispatch = useDispatch()

    const {handleSubmit, control} = useForm<NotificationsFormEmailProps>({
        defaultValues: {
            onIncomeMail: onIncomeMail,
            onContractIncomeMail: onContractIncomeMail,
            onSuccessWithdrawMail: onSuccessWithdrawMail,
            onStartBonusMail: onStartBonusMail,
            onNewItemsInCalendarMail: onNewItemsInCalendarMail,
            onEventMail: onEventMail,
            onCourseMail: onCourseMail,
            onWebinarsMail: onWebinarsMail,
            onNewsMail: onNewsMail
        },
        mode: 'onChange',
    });

    console.log(onIncomeMail, onContractIncomeMail, onSuccessWithdrawMail, onStartBonusMail,
        onNewItemsInCalendarMail, onEventMail, onCourseMail, onWebinarsMail, onNewsMail)

    const onSubmit = (data: NotificationsFormEmailProps) => {
        onSubmitHandle(data);
    }

    const toggleAllNotifications = (value: boolean) => {
        dispatch(toggleNotificationsEmailForm({value: value}))
    }

    return (
        <form onSubmit={handleSubmit(onSubmit)}>
            <SwitchBlock activeValue={'Включить'} passiveValue={'Выключить'} onChange={toggleAllNotifications}/>
            <div className={style.formItem_wrapper}>
                <div>
                    <FormItem<NotificationsFormEmailProps> name={'onNewsMail'} control={control} label={'Новости'}/>
                    <FormItem<NotificationsFormEmailProps> name={'onWebinarsMail'} control={control} label={'Вебинары'}/>
                    <FormItem<NotificationsFormEmailProps> name={'onCourseMail'} control={control} label={'Курсы'}/>
                    <FormItem<NotificationsFormEmailProps> name={'onEventMail'} control={control} label={'События'}/>
                    <FormItem<NotificationsFormEmailProps> name={'onNewItemsInCalendarMail'} control={control}
                                              label={'Новые отметки в календаре'}/>
                </div>
                <div className="">
                    <FormItem<NotificationsFormEmailProps> name={'onStartBonusMail'} control={control}
                                              label={'Успешное снятие средств'}/>
                    <FormItem<NotificationsFormEmailProps> name={'onIncomeMail'} control={control} label={'Пополнение счета'}/>
                    <FormItem<NotificationsFormEmailProps> name={'onIncomeMail'} control={control} label={'Реферальные начисления'}/>
                    <FormItem<NotificationsFormEmailProps> name={'onContractIncomeMail'} control={control}
                                              label={'Начисления по контрактам'}/>
                </div>
            </div>
            <TakeOutButton color={'blue'}>
                сохранить
            </TakeOutButton>
        </form>

    )
}
