import {createAsyncThunk} from '@reduxjs/toolkit';
import { baseRequestAxios } from '../../../api/baseRequest';

export const checkSmsCode = createAsyncThunk(
    'checkSmsCode',
    async (phoneAndCode: { phone: string, code: number }, {rejectWithValue}) => {
        try {
            const response = await baseRequestAxios.post<{ secretCode: string }>("/registration/check-sms-code", {
                phone: phoneAndCode.phone,
                code: phoneAndCode.code
            })
            if (response.status === 200) {
                return response.data
            }
        } catch (errors) {
            return rejectWithValue(errors)
        }
    }
)