import style from '../security.module.css'
import {SwitchButton} from "../../../../../../ui/buttons/swichButton/switchButton";
import switchIcon from '../../../../icon/iconSwitcher.svg'

interface Props {
    onChange: (value: boolean) => void
}

export const AuthenticationToggle = ({onChange}: Props): JSX.Element => {
    return (
        <div className={style.authentication_toggleWrapper}>
        <SwitchButton onChange={onChange} icon={switchIcon} />
            <span>Включить</span>
        </div>
    )
}