import styles from './BuyContracts.module.scss';
import { useState } from "react"

import { ReactSVG } from 'react-svg';
import infoIcon from '../../icons/info.svg';
import contractIcon from '../../icons/contract.svg';
import reverseIcon from '../../icons/reverse.svg';
import DNCCoin from '../../../dashboard/icons/DNCBalance.svg';
import minusIcon from '../../icons/minusBlock.svg';
import plusIcon from '../../icons/plusBlock.svg';

import {buyContractsThunk} from '../../../../modules/contracts/buyContracts/buyContractsThunk'
import {useDispatch} from "react-redux";
import {useEffect} from "react";

interface Props {
  handleIsContractBought: () => void;
}

export const BuyContracts = ({handleIsContractBought}: Props) => {
  const [chooseAmount, setChooseAmount] = useState(0);
  const [bought, setBought] = useState(false);
  const [isAcceptAnAgreement, setIsAcceptAnAgreement] = useState(false);
  const dispatch = useDispatch()

useEffect(() => {
  if (bought) {
    dispatch(buyContractsThunk({
      contract_type: 3,
      count: chooseAmount,
    }));
    setBought(false);
    setChooseAmount(0);
    handleIsContractBought();
  }
}, [bought, chooseAmount, dispatch, handleIsContractBought])

const handleAccept = () => {
  setIsAcceptAnAgreement(!isAcceptAnAgreement);
}

  const changeAmount = (type: string) => {
    if (type === 'minus') {
      if (chooseAmount > 1) {
        setChooseAmount(chooseAmount - 1)
      }
    }
    if (type === 'plus') {
      setChooseAmount(chooseAmount + 1)
    }
  }

  return (
    <div className={styles.buyContracts}>
      <div className={styles.header}>
        <div className={styles.infoString}>
          <ReactSVG className={styles.infoIcon} src={infoIcon} />
          Условия контракта
        </div>
          <p className={styles.subtitle}>Контракт</p>
          <p className={styles.name}>"РАЗУМНЫЙ"</p>
      </div>
      <div className={styles.main}>
        <ReactSVG className={styles.contractIcon} src={contractIcon} />
        1 Контракт
        <ReactSVG className={styles.reverseIcon} src={reverseIcon} />
        <ReactSVG className={styles.DNCCoin} src={DNCCoin} />
        500 DNC
      </div>
      <p className={styles.chooseAmount}>Выберите количество контрактов</p>
      <div className={styles.count}>
        <div className={styles.minusBlock}>
        <ReactSVG onClick={() => changeAmount('minus')} className={styles.countIcon} src={minusIcon} />
        </div>
        {chooseAmount}
        <div className={styles.plusBlock}>
        <ReactSVG onClick={() => changeAmount('plus')} className={styles.countIcon} src={plusIcon} />
        </div>
      </div>
      <div className={styles.buttonBlock}>
        <div className={styles.rules}>
          <input
            onClick={() => handleAccept()}
            type='checkbox'
            className={styles.checkbox}
          ></input>
          <div className={styles.rulesText}>
          Я прочитал и согласен с <span>пользовательским соглашением, условиями контракта</span> и <span>политикой рисков</span>
          </div>
        </div>
        {isAcceptAnAgreement
          ? (
          <div className={styles.button} onClick={() => setBought(true)}>
            <div className={styles.left}>
              Присоединиться
            </div>
            <div className={styles.right}>
              <div>
                <span>{chooseAmount * 500}</span> 
                <ReactSVG className={styles.DNCCoin} src={DNCCoin} />
              </div>
              Общая цена
            </div>
          </div>)
          : (
            <div className={styles.fakeButton}>
              <div className={styles.wrapper}></div>
              <div className={styles.left}>
                Присоединиться
              </div>
              <div className={styles.right}>
                <div>
                  <span>{chooseAmount * 500}</span> 
                  <ReactSVG className={styles.DNCCoin} src={DNCCoin} />
                </div>
                Общая цена
              </div>
            </div>)}
      </div>
    </div>
  )
}
