import {createSlice} from '@reduxjs/toolkit';
import {structureBonuses} from './structureBonusesThunk';
import {StructureBonuses} from "../../../api/type/structure";

const structureBonusesSlice = createSlice({
    name: 'structureBonuses',
    initialState: {
        bonuses: {} as StructureBonuses,
        error: false,
        loading: false
    },
    reducers: {},
    extraReducers: (builder) => {
        builder.addCase(structureBonuses.pending, (state) => {
            state.loading = true;
        });
        builder.addCase(structureBonuses.fulfilled, (state, action) => {
            if (action.payload) {
                state.bonuses = action.payload
                state.loading = false
            }
        });
        builder.addCase(structureBonuses.rejected, (state) => {
            state.loading = false;
            state.error = true;
        });
    },

});

export const structureBonusesReducer = structureBonusesSlice.reducer;