import {createSlice} from '@reduxjs/toolkit';
import {nextLevelBlock} from './nextLevelBlockThunk';
import {NextLevelBlock} from "../../../api/type/structure";

const nextLevelBlockSlice = createSlice({
    name: 'nextLevelBlockSlice',
    initialState: {
        nextLevelBlock: {} as NextLevelBlock,
        error: false,
        loading: false
    },
    reducers: {},
    extraReducers: (builder) => {
        builder.addCase(nextLevelBlock.pending, (state) => {
            state.loading = true;
        });
        builder.addCase(nextLevelBlock.fulfilled, (state, action) => {
            if (action.payload) {
                state.nextLevelBlock = action.payload
                state.loading = false
            }
        });
        builder.addCase(nextLevelBlock.rejected, (state) => {
            state.loading = false;
            state.error = true;
        });
    },

});

export const nextLevelBlockSliceReducer = nextLevelBlockSlice.reducer;