import style from './steppper.module.css'
import {ReactSVG} from "react-svg";
import {Step, Stepper} from 'react-form-stepper';
import {ConnectorStyleProps} from 'react-form-stepper/dist/components/Connector/ConnectorTypes';
import {StepStyleDTO} from 'react-form-stepper/dist/components/Step/StepTypes';
import iconStepEdit from '../../../../assets/icons/iconStepEdit.svg'
import iconStepCheck from '../../../../assets/icons/iconStepChech.svg'

export const StepperKebab = ({activeStep}: { activeStep: number }): JSX.Element => {
    const step = [1, 2, 3, 4, 5]


    const stepStyleDTO: StepStyleDTO = {
        activeBgColor: 'transparent',
        activeTextColor: 'rgba(255, 255, 255, 0.6)',
        completedBgColor: '#39D2D1',
        completedTextColor: 'transparent',
        inactiveBgColor: '#0795CF',
        inactiveTextColor: 'transparent',
        size: 17,
        circleFontSize: '5rem',
        labelFontSize: '14px',
        borderRadius: '50%',
        fontWeight: 600,
    }

    const connectorStyle: ConnectorStyleProps = {
        disabledColor: 'rgba(255, 255, 255, 0.2)',
        activeColor: '#39D2D1',
        completedColor: '#39D2D1',
        size: 3,
        stepSize: '14px',
        style: 'solid',
    }

    return (
        <div className={style.stepperKebab}>
            <Stepper className={style.stepperWrapper}
                     styleConfig={stepStyleDTO}
                     connectorStyleConfig={connectorStyle}
                     connectorStateColors={true}
                     activeStep={activeStep}
            >
                {step.map((step) =>
                    <Step key={step} className={style.step}>
                        <span className={style.steps_label}>{`Шаг ${step}`}</span>
                        {step === activeStep+1 &&
                        (<ReactSVG src={iconStepEdit} className={style.iconStepEdit}/>)}
                        {step <= activeStep &&
                        (<ReactSVG src={iconStepCheck} className={style.iconStepCheck}/>)}
                    </Step>
                )}
            </Stepper>
        </div>
    )
}