import { createAsyncThunk } from '@reduxjs/toolkit';
import { baseRequestAxios } from '../../../api/baseRequest';
import {BuyContractsRequest, BuyContractsResponse} from "../../../api/type/contracts/buyContracts";

export const buyContractsThunk = createAsyncThunk(
    'buyContracts',
    async (requestData: BuyContractsRequest, {rejectWithValue}) => {
        try {
            const response  = await baseRequestAxios.post<BuyContractsResponse>("/api/contract/buy", {
              contract_type: requestData.contract_type,
              count: requestData.count,
            })
            if (response.status === 200) {
                return response.data
            }
        } catch (errors){
            return  rejectWithValue(errors)
        }
    }
)
