import { createSlice } from '@reduxjs/toolkit';
import { axiosHeadersUpdater } from '../../../api/baseRequest';
import { WalletTransactionsResponse } from '../../../api/type/wallet/transactions';
import { WalletTransactionsThunk } from './walletTransactionsThunk';

const walletTransactionsSlice = createSlice({
    name: 'walletTransactions',
    initialState: {
      transactions: {} as WalletTransactionsResponse,
      loading: false,
    },
    reducers: {},
    extraReducers: (builder) => {
        builder.addCase(WalletTransactionsThunk.pending, (state) => {
            state.loading = true;
            axiosHeadersUpdater()
        });
        builder.addCase(WalletTransactionsThunk.fulfilled, (state, action) => {
            if (action.payload) {
                state.transactions = action.payload;
            }
        });
        builder.addCase(WalletTransactionsThunk.rejected, (state) => {});
    },

});

export const walletTransactionsReducer = walletTransactionsSlice.reducer;