import style from './structureItem.module.css'
import {ButtonFilterDay} from "../../../../ui/buttons/buttonFilterDay/buttonsFilterDay";
import {Line} from "../../../../api/type/structure";
import {useState} from "react";

interface Props {
    lines: Line[]
}

export const StructureItem = ({lines}: Props): JSX.Element => {
    const [initialValue, setInitialValue] = useState<'day' | 'month' | 'year'>('day')

    const setFilterData = (value: 'day' | 'month' | 'year') => {
        setInitialValue(value)
    }

    return (
        <div className={style.structureItemWrapper}>
            <div className={style.structureHeader}>
                <span>Струкутра</span>
                <div className={style.filter}>
                    <ButtonFilterDay isActive={initialValue === 'day'}
                                     onClick={() => setFilterData('day')}>День
                    </ButtonFilterDay>
                    <ButtonFilterDay isActive={initialValue === 'month'}
                                     onClick={() => setFilterData('month')}>Месяц
                    </ButtonFilterDay>
                    <ButtonFilterDay isActive={initialValue === 'year'}
                                     onClick={() => setFilterData('year')}>Год
                    </ButtonFilterDay>
                </div>
            </div>
            <div className={style.structureList}>
                <div className={style.structureListTitle}>
                    <span>Линия</span>
                    <span>Партеры</span>
                    <span>Вознаграждение</span>
                </div>
                <div className={style.structureCard}>
                    {lines && lines.map((item) =>
                        <div key={item.line} className={style.structureListCard}>
                            <span className={style.line}>#{item.line}</span>
                            <div className={style.partners}>{ `${item.partners === 0? 'У вае еще нет партнеров' : item.partners}` }</div>
                            <span className={style.reward}>
                                {initialValue === 'day' ? (<span>{item.day}</span>) :
                                    initialValue === 'year' ? (<span>{item.year}</span>) :
                                        (<span>{item.month}</span>)}
                                DNC</span>
                        </div>
                    )}
                </div>
            </div>
        </div>
    )
}