import style from "../../structure.module.css";
import {ReactSVG} from "react-svg";
import iconCopyLink from "../../../../assets/icons/iconCopyLink.svg";

export const ReferralLink = (): JSX.Element => {
  return (
      <div className={style.cardWrapper}>
          <div className={style.cardTop}>
              <ReactSVG className={style.iconCopyLink} src={iconCopyLink}/>
              <span>Реферальная ссылка</span>
          </div>
          <div className={style.cardBottom}>
              <h3>Реферальная ссылка</h3>
              <div className={style.cardBottom_block}>
                  <span>mango.trade/cooperarenko</span>
              </div>
          </div>
      </div>
  )
}