import style from "../../structure.module.css";
import {ReactSVG} from "react-svg";
import iconMyLevel from "../../../../assets/icons/iconMyLevel.svg";
import iconMyLevelSmall from "../../../../assets/icons/iconMyLevelSmall.svg";
import iconCheckMyLevel from "../../../../assets/icons/iconCheckMyLevel.svg";
import iconInfoBig from "../../../../assets/icons/iconInfoBig.svg";
import {MyLevelCardBody} from "../myLevelCardBody/myLevelCardBody";
import {useSelector} from "react-redux";
import {AppStateType} from "../../../../core/redux/store";

export const MyLevelCard = (): JSX.Element => {
    const {level} = useSelector((state: AppStateType) => state.user.user)

    return (
        <div className={style.myLevelBlock}>
            <div className={style.myLevelBlock_top}>
                <div className={style.myLevelStatus}>
                    <ReactSVG className={style.iconMyLevel} src={iconMyLevel}/>
                    <span>{level}</span>
                </div>
                <div className={style.aboutMy}>
                    <span>Мой уровень</span>
                    <h3>lvl {level} — Новичек</h3>
                </div>
            </div>
            <div className={style.myLevelBlock_body}>
                <div className={style.myLevelBlock_body_top}>
                    <ReactSVG className={style.iconInfo} src={iconInfoBig}/>
                    <span>Для повышения уровня <br/> выполните условия</span>
                </div>
                <div className={style.myLevelBlock_body_card}>
                    <MyLevelCardBody currentValue={'200'} title={'Инвестируйте'} widthBlock={'70'}
                                     subTitle={'500'} />

                    <MyLevelCardBody currentValue={'4 000'} title={'Обьем в баллах'}
                                     widthBlock={'30'}
                                     subTitle={'10 000'}/>

                    <MyLevelCardBody currentValue={'1'} title={'Первая линия'} widthBlock={'70'}
                                     subTitle={<StatisticLevel />}/>

                    <MyLevelCardBody currentValue={<ReactSVG src={iconCheckMyLevel}/>}
                                     title={'Ветки'} widthBlock={'100'}
                                     subTitle={'Партнеров на 2 линии'}/>
                </div>
            </div>
        </div>
    )
}

const StatisticLevel = ({value1 = 2, value2 = 1}): JSX.Element => {

    return (
        <div className={style.statisticLevel}>
            <div className={style.statisticLevel_leftBlock}>
                <ReactSVG className={style.iconMyLevelSmall} src={iconMyLevelSmall}/>
                <span>{ value1} чел.</span>
            </div>
            <div className={style.statisticLevel_rightBlock}>
                <ReactSVG className={style.iconMyLevelSmall} src={iconMyLevelSmall}/>
                <span>{ value2} чел.</span>
            </div>
        </div>
    )
}