import {createSlice, PayloadAction} from '@reduxjs/toolkit';
import {getUserDataThunk} from './getUserDataThunk';
import {axiosHeadersUpdater} from '../../../api/baseRequest';
import {
    NotificationsFormEmailProps,
    NotificationsFormTgProps,
    PersonalDataFormProps,
    SocialNetworkFormProps,
    UserResponse
} from "../../../api/type/user";

const getUserDataSlice = createSlice({
    name: 'getUserData',
    initialState: {
        user: {} as UserResponse,
        loading: false,
        error: false,
        isChangedData: false
    },
    reducers: {
        toggleIsChangedData: (state, action: PayloadAction<{ value: boolean }>) => {
            //state.isChangedData = action.payload.value
        },
        upDatePersonalData: (state, action: PayloadAction<PersonalDataFormProps>) => {
            state.user.phone = action.payload.phone;
            state.user.country = action.payload.country;
            state.user.email = action.payload.email;
            //state.isChangedData = true
        },
        updateSocialNetwork: (state, action: PayloadAction<SocialNetworkFormProps>) => {
            state.user.facebook = action.payload.facebook
            state.user.telegram = action.payload.telegram
            state.user.instagram = action.payload.instagram
            state.user.twitter = action.payload.twitter
            //state.isChangedData = true
        },
        updateNotificationsEmailForm: (state, action: PayloadAction<NotificationsFormEmailProps>) => {
            state.user.onIncomeMail = action.payload.onIncomeMail;
            state.user.onContractIncomeMail = action.payload.onContractIncomeMail;
            state.user.onSuccessWithdrawMail = action.payload.onSuccessWithdrawMail;
            state.user.onStartBonusMail = action.payload.onStartBonusMail;
            state.user.onNewItemsInCalendarMail = action.payload.onNewItemsInCalendarMail;
            state.user.onEventMail = action.payload.onEventMail;
            state.user.onCourseMail = action.payload.onCourseMail;
            state.user.onWebinarsMail = action.payload.onWebinarsMail;
            state.user.onNewsMail = action.payload.onNewsMail
            state.isChangedData = true
        },
        toggleNotificationsEmailForm: (state, action: PayloadAction<{value: boolean}>) => {
            state.user.onIncomeMail = action.payload.value;
            state.user.onContractIncomeMail = action.payload.value;
            state.user.onSuccessWithdrawMail = action.payload.value;
            state.user.onStartBonusMail = action.payload.value;
            state.user.onNewItemsInCalendarMail = action.payload.value;
            state.user.onEventMail = action.payload.value;
            state.user.onCourseMail = action.payload.value;
            state.user.onWebinarsMail = action.payload.value;
            state.user.onNewsMail = action.payload.value
        },
        updateNotificationsTgForm: (state, action: PayloadAction<NotificationsFormTgProps>) => {
            state.user.onIncomeTg = action.payload.onIncomeTg;
            state.user.onContractIncomeTg = action.payload.onContractIncomeTg;
            state.user.onSuccessWithdrawTg = action.payload.onSuccessWithdrawTg;
            state.user.onStartBonusTg = action.payload.onStartBonusTg;
            state.user.onNewItemsInCalendarTg = action.payload.onNewItemsInCalendarTg;
            state.user.onEventTg = action.payload.onEventTg;
            state.user.onCourseTg = action.payload.onCourseTg;
            state.user.onWebinarsTg = action.payload.onWebinarsTg;
            state.user.onNewsTg = action.payload.onNewsTg
            //state.isChangedData = true
        },
        toggleNotificationsTgForm: (state, action: PayloadAction<{value: boolean}>) => {
            state.user.onIncomeTg = action.payload.value;
            state.user.onContractIncomeTg = action.payload.value;
            state.user.onSuccessWithdrawTg = action.payload.value;
            state.user.onStartBonusTg = action.payload.value;
            state.user.onNewItemsInCalendarTg = action.payload.value;
            state.user.onEventTg = action.payload.value;
            state.user.onCourseTg = action.payload.value;
            state.user.onWebinarsTg = action.payload.value;
            state.user.onNewsTg = action.payload.value
            //state.isChangedData = true
        },
    },
    extraReducers: (builder) => {
        builder.addCase(getUserDataThunk.pending, (state) => {
            axiosHeadersUpdater()
            state.loading = true
        });
        builder.addCase(getUserDataThunk.fulfilled, (state, action) => {
            if (action.payload) {
                state.user = action.payload;
                state.loading = false;
                state.isChangedData = false;
            }
        });
        builder.addCase(getUserDataThunk.rejected, (state) => {
            state.loading = false;
            state.error = true
        });
    },
});
export const {
    upDatePersonalData, updateSocialNetwork, toggleIsChangedData, updateNotificationsEmailForm,
    updateNotificationsTgForm, toggleNotificationsEmailForm, toggleNotificationsTgForm
} = getUserDataSlice.actions;
export const getUserDataReducer = getUserDataSlice.reducer;