import styles from './eventsList.module.scss';
import { ReactSVG } from 'react-svg';
import calendarIcon from '../icons/calendar.svg';
import clockIcon from '../icons/clock.svg';

interface Props {
  events: oneEvent[];
}

export const EventsList = ({ events }: Props) => {
  return (
    <ul className={styles.eventList}>
      {events.map(event => (
        <li className={styles.eventItem} key={event.id}>
          <div className={styles.dateBlock}>
            <div className={styles.date}>
              <ReactSVG className={styles.calendarIcon} src={calendarIcon} />
              {event.date.slice(0, 10)}
            </div>
            <div className={styles.time}>
              <ReactSVG className={styles.clockIcon} src={clockIcon} />
              {event.date.slice(10)}
            </div>
          </div>
          <div className={styles.info}>
            <div className={styles.type}>Курс</div>
            <div className={styles.name}>{event.ruTitle}</div>
          </div>
        </li>
      ))}
    </ul>
  )
}
