import { Layout } from "../../ui/layout/layout"
import styles from './calendar.module.scss';

import {useDispatch, useSelector} from "react-redux";
import {AppStateType} from "../../core/redux/store";
import {useEffect} from "react";
import {eventsThunk} from "../../modules/calendar/events/eventsThunk";
import {EventsList} from './eventsList/eventsList';
import { CalendarComponent } from './calendarComponent/classComponent';

export const Calendar = (): JSX.Element => {
  const { items } = useSelector((state: AppStateType) => state.events.events);
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(eventsThunk({
      limit: 50,
      offset: 1,
    }));
}, [dispatch]);

    return (
        <Layout>
            <div className={styles.CalendarPage}>
              <div className={styles.eventsBlock}>
                <h2 className={styles.eventsTitle}>
                  Ближайшие события
                </h2>
                <div className={styles.eventsList}>
                  {items && (<EventsList events={items} />)}
                </div>
              </div>
              <div className={styles.calendarBlock}>
                  <CalendarComponent items={items}/>
              </div>
            </div>
        </Layout>
    )
}
