import styles from './rejectContract.module.scss';
import RejectButton from '../../../ui/buttons/BuyButton/BuyButton';
import { ReactSVG } from 'react-svg';
import rejectIcon from '../icons/rejectIcon.svg';
import { rejectContractThunk } from '../../../modules/contracts/rejectContract/rejectContractThunk';
import { useDispatch } from 'react-redux';

interface Props {
  toggleRejectBlock: () => void;
  id: string;
}

export const RejectContract = ({toggleRejectBlock, id}: Props) => {
  const dispatch = useDispatch()

  const rejectClick = (id: string) => {
    dispatch(rejectContractThunk({
      contract_id: id,
    }));
    toggleRejectBlock();
  }
  
  return (
    <div className={styles.rejectContract}>
      <div className={styles.headerBlock}>
        <h2 className={styles.title}>Расторжение контракта</h2>
        <ReactSVG className={styles.rejectIcon} src={rejectIcon} onClick={() => toggleRejectBlock()}/>
      </div>
      <p className={styles.info}>Я принимаю во внимание и соглашаюсь со всеми антикризисными мерами, принятыми в работе платформой ManGo.Trade в период реабилитационного периода, а именно:</p>
        <div className={styles.block}>
          <input
            onClick={() => {}}
            type='checkbox'
            className={styles.checkbox}
          ></input>
           <p className={styles.rule}>
              Срок закрытия каждого Контракта составляет 60 календарных дней.
           </p>
        </div>
        <div className={styles.block}>
          <input
            onClick={() => {}}
            type='checkbox'
            className={styles.checkbox}
          ></input>
            <p className={styles.rule}>
              Закрытия каждого Контракта происходит с учетом дисконта в 70% от его номинальности.
           </p>

        </div>
        <div className={styles.block}>
          <input
            onClick={() => {}}
            type='checkbox'
            className={styles.checkbox}
          ></input>
            <p className={styles.rule}>
              После проведения процедуры закрытия Контракта на счет держателя будет переведено 30% от номинальности Контракта.
           </p>

        </div>
        <div className={styles.buttonBlock}>
          <RejectButton color='green' onClick={() => rejectClick(id)}>
            ПРЕКРАТИТЬ
          </RejectButton>
        </div>
    </div>
  )
}
