import style from './documents.module.css'
import {Card} from "./card";

export const Documents = (): JSX.Element => {
    return (
        <div className={style.documents}>
            <h4>Договоры и оферты</h4>
            <div className={style.cardWrapper}>
                <Card title={'Политика использования файлов Cookie'} />
                <Card title={'Пользовательское соглашение ManGo Trade'} />
                <Card title={'Политика риска ManGo Trade'} />
                <Card title={'Политика использования файлов Cookie'} />
            </div>
        </div>
    )
}