import {FC} from "react"
import {ReactSVG} from 'react-svg';
import DNCBalanceIcon from '../icons/DNCBalance.svg';
import scheduleIcon from '../icons/schedule.svg'
import numbersIcon from '../icons/numbers.svg'
import styles from './DNCBalance.module.scss';

interface Props {
    title: string,
    income: string,
    balance: number
}


export const DNCBalance: FC<Props> = (props): JSX.Element => {
    const {income, title, balance, children} = props

    return (
        <div className={styles.wrapper}>
            <div className={styles.DNCBalance}>
                <div className={styles.blockName}>
                    <ReactSVG className={styles.DNCBalanceIcon} src={DNCBalanceIcon}/>
                    <span> Баланс {title}</span>
                </div>
                <div className={styles.balance}>
                    {balance}
                </div>
                <div className={styles.todayIncome}>
                    <span>Сегодня: </span>{`+${income} DNC`}
                </div>
                <div className={styles.schedule}>
                    <ReactSVG className={styles.scheduleIcon} src={scheduleIcon}/>
                    <ReactSVG className={styles.numbers} src={numbersIcon}/>
                </div>
                {children}
            </div>
        </div>
    )
}
