import {FC} from "react";
import style from './buttonFilterData.module.css'

interface Props {
    onClick?: () => void,
    isActive?: boolean,
}

export const ButtonFilterDay: FC<Props> = ({onClick, isActive, children}) => {

    return (
        <button className={`${style.btnFilter} ${isActive ? style.active: ''}`} onClick={onClick}>{children}</button>
  )
}