import {createSlice} from '@reduxjs/toolkit';
import {StructurePartners} from "../../../api/type/structure";
import {structurePartnersThunk} from './structurePartnersThunk';

const structurePartnersSlice = createSlice({
    name: 'structurePartners',
    initialState: {
        partners: {} as StructurePartners,
        error: false,
        loading: false
    },
    reducers: {},
    extraReducers: (builder) => {

        builder.addCase(structurePartnersThunk.pending, (state) => {
            state.loading = true;
        });
        builder.addCase(structurePartnersThunk.fulfilled, (state, action) => {
            if (action.payload) {
                state.partners = action.payload
                state.loading = false
            }
        });
        builder.addCase(structurePartnersThunk.rejected, (state) => {
            state.loading = false;
            state.error = true;
        });
    },

});

export const structurePartners = structurePartnersSlice.reducer;