import {useState} from "react";
import style from "../../pages/settings/components/pages/notifications/notifications.module.css";
import {SwitchButton} from "../buttons/swichButton/switchButton";
import Bell from "../../pages/settings/icon/iconBell.svg";

interface Props {
    onChange: (value: boolean) => void,
    activeValue: string,
    passiveValue: string,
}

export const SwitchBlock = ({activeValue, passiveValue, onChange}: Props): JSX.Element => {
    const [isActiveNotifications, setIsActiveNotifications] = useState(false)

    const onChangeActiveNotifications = () => {
        setIsActiveNotifications(prev => !prev)
        onChange(isActiveNotifications)
    }
    return (
        <div className={style.switchBlock}>
            <span>{`${isActiveNotifications? passiveValue : activeValue}`}</span>
            <SwitchButton onChange={onChangeActiveNotifications} icon={Bell}/>
        </div>
    )
}