import {createSlice} from '@reduxjs/toolkit';
import {PartnersThunk} from './partnersThunk';

const PartnersSlice = createSlice({
    name: 'partners',
    initialState: {
        partners: [] as Partner[],
        loading: false,
        error: null,
    },
    reducers: {
        addArticles(state, action) {
            state.partners = action.payload;
        }
    },
    extraReducers: (builder) => {
        builder.addCase(PartnersThunk.pending, (state) => {
            state.loading = true;
            state.error = null;
        });
        builder.addCase(PartnersThunk.fulfilled, (state, action) => {
            if (action.payload) {
                state.partners = action.payload.items;
            }
        });
        builder.addCase(PartnersThunk.rejected, (state) => {
          state.loading = false
        });
    },
});

export const partnersReducer = PartnersSlice.reducer;