import {FC} from 'react';
import {ReactSVG} from 'react-svg';
import styles from './TotalContracts.module.scss';

type Props = {
    text?: string;
    sum?: number;
    icon: string;
}

export const TotalContracts: FC<Props> = (props) => {
    const {icon, sum, text} = props

    return (
        <div className={styles.wrapper}>
            <div className={styles.TotalContracts}>
                <div className={styles.iconBox}>
                    <ReactSVG className={styles.notebookIcon} src={icon}/>
                </div>
                <div className={styles.sum}>
                    {sum}
{/*                     <ReactSVG className={styles.currencyIcon} src={'' +
                    ''}/> */}
                </div>
                <div className={styles.text}>
                    <span>{text}</span> 
                </div>
            </div>
        </div>
    )
}
