import styles from './smartComponent.module.scss';

import {DNCBalance} from '../../dashboard/DNCBalance/DNCBalance';
import {TotalContracts} from "../../dashboard/TotalContracts/TotalContractsSample/TotalContracts";
import { BuyContracts } from '../buyContracts/BuyContracts/BuyContracts';
import { ContractList } from '../ContractList/ContractList';
import { IncomeList } from '../IncomeList/IncomeList';
import {ButtonsGroup} from "../../../ui/buttons/buttonsGroup/buttonsGroup";

import notebookIcon from '../../dashboard/icons/notebook.svg';
import { useDispatch, useSelector } from 'react-redux';
import { AppStateType } from '../../../core/redux/store';
import { useEffect, useState } from 'react';
import { getUserDataThunk } from '../../../modules/user/getUserData/getUserDataThunk';
import { contractsThunk } from '../../../modules/contracts/contractsThunk';

export const SmartComponent = () => {
  const {
    balance,
    rzmBalance,
    amountOfContract
} = useSelector((state: AppStateType) => state.user.user);
  const { items, count } = useSelector((state: AppStateType) => state.contracts.contracts);
  const [isContractBought, setIsContractBought] = useState(false);
  const dispatch = useDispatch()

  useEffect(() => {
    dispatch(getUserDataThunk());
    dispatch(contractsThunk({
      contract_type: 3,
      limit: 10000,
      offset: 0,
    }));
    setIsContractBought(false);
  }, [isContractBought, balance, dispatch])

  const handleIsContractBought = () => {
    setIsContractBought(true);
  }

  return (
    <>
      {count && (
        <div>
          <div className={styles.PanelBlock}>
          <div className={styles.balance}>
              <DNCBalance balance={balance} title={'DNC'} income={'0'}>
                  <ButtonsGroup/>
              </DNCBalance>

              <DNCBalance balance={rzmBalance} title={'RZM'} income={'124'}>
                  <ButtonsGroup/>
              </DNCBalance>
          </div>
          <div className={styles.otherBlocks}>
              <div className={styles.contracts}>
                  <TotalContracts text='Общая сумма контрактов' sum={amountOfContract} icon={notebookIcon}/>
                  <TotalContracts text='Всего контрактов' sum={count} icon={notebookIcon}/>
              </div>
              <div className={styles.BuyContracts}>
                  <BuyContracts handleIsContractBought={handleIsContractBought}/>
              </div>
          </div>
          </div>
          <h2 className={styles.title}>
            Доход по контрактам
          </h2>
          <IncomeList items={items} count={count}/>
          <h2 className={styles.title}>
            Мои контракты
          </h2>
          <ContractList items={items} count={count}/>
        </div>
      )}
  </>
  )
}
