import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { AppStateType } from "../../core/redux/store";
import { WalletTransactionsThunk } from "../../modules/history/wallet/walletTransactionsThunk";
import { Layout } from "../../ui/layout/layout"
import styles from './history.module.scss'
import { HistoryList } from './historyList/HistoryList';
import { Statistic } from './statistic/Statistic';

export const History = (): JSX.Element => {
  const {items, count} = useSelector((state: AppStateType) => state.walletTransactions.transactions);
  const dispatch = useDispatch()
  
  useEffect(() => {
      dispatch(WalletTransactionsThunk({
        limit: 10000,
        offset: 0,
      }));
  }, [dispatch])

  return (
      <Layout>
        <div className={styles.historyPage}>
          {count && (
            <div className={styles.HistoryList}>
            <HistoryList items={items} count={count}/>
          </div>
          )}
          <div className={styles.Statistic}>
            <Statistic items={items}/>
          </div>
        </div>
      </Layout>
  )
}
