import {FC} from "react";
import style from "./formItem.module.css";

interface PropsFormItemInput {
    label: string,
    name: string,
}

export const FormItem: FC<PropsFormItemInput> = (props): JSX.Element => {
    const {label, name, children} = props
    return (
        <div className={style.formItem}>
            <label htmlFor={name}>{label}</label>
            <div className={style.wrapperInputs}>
                {children}
            </div>
        </div>
    )
}