import styles from './IncomeItem.module.scss';
import { ReactSVG } from 'react-svg';
import incomeCoin from '../../../dashboard/icons/DNCBalance.svg';

interface Props {
  item: Contract
}

export const IncomeItem = ({item}: Props) => {
  const { id, amount, percent, dateCreate } = item;

  return (
    <li className={styles.item}>
      <div className={styles.firstPart}>
        <div className={styles.index}>
          № {id}
        </div>
        <div className={styles.MobileDate}>
          {dateCreate}
          <span className={styles.mobileText}>Дата</span>
        </div>
        <div className={styles.amount}>
          {amount}
          <ReactSVG className={styles.coin} src={incomeCoin} />
        </div>
      </div>
      <div className={styles.secondPart}>
        <div className={styles.MobileAmount}>
          <div>
            {amount}
            <ReactSVG className={styles.coin} src={incomeCoin} />
          </div>
          <span className={styles.mobileText}>Сумма</span>
        </div>
        <div className={styles.income}>
          <div>
            {percent}%
          </div>
          <span className={styles.mobileText}>income</span>
        </div>
        <div className={styles.date}>
          {dateCreate}
        </div>
      </div>
    </li>
  )
}

