import React, { useState } from 'react';
import styles from './ContractList.module.scss';
/* import {useState, useEffect} from "react" */
import { ContractItem } from './ContractItem/ContractItem';
import { ReactSVG } from 'react-svg';
import filterIcon from '../icons/filterIcon.svg';
import ActiveFilterIcon from '../icons/ActiveFilterIcon.svg';
import { Pagination } from '../../../ui/pagination/pagination';

interface Props {
  items: Contract[];
  count: number;
}

export const ContractList = ({items, count}: Props) => {
  const goodsList = [...items];
  const [sortBy, setSortBy] = useState('');
  const [selectedContracts, setSelectedContracts] = useState(0)
  const [isReverse, setIsReverse] = useState(false);

const setSortType = (type: string) => {
  if (sortBy === type) {
    setIsReverse(!isReverse);
  }

  setSortBy(type);
}

const checkStatus = (list: Contract[]) => {
  if (sortBy) {
    list.sort((first, second) => {
      switch (sortBy) {
        case 'index':
          return first.id.localeCompare(second.id);

        case 'amount':
          return first.amount - second.amount;

        case 'incomeDNC':
          return first.dailyIncome - second.dailyIncome;

        case 'incomeRZM':
          return first.rzmDailyIncome - second.rzmDailyIncome;

        case 'date':
          return second.dateCreate.localeCompare(first.dateCreate);

        default:
          return 0;
      }
    });
  }
};

checkStatus(goodsList);

if (isReverse) {
  goodsList.reverse();
}

const handlePageClick = (selected: number) => {
  setSelectedContracts(selected);
}

const showAmountOfContracts = (number: number) => {
  const start = number * 5;
  const end = start + 5;

  return goodsList.slice(start, end);
}

return (
  <div className={styles.contractListBlock}>
    <div className={styles.panel}>
      <div className={styles.sortByIndex} onClick={() => (setSortType('index'))}>
        Номер транзакции
        {(sortBy === 'index') && (<ReactSVG className={styles.ActiveFilterIcon} src={ActiveFilterIcon} />)}
        {(sortBy !== 'index') && (<ReactSVG className={styles.filterIcon} src={filterIcon} />)}
      </div>
      <div className={styles.sortByAmount} onClick={() => (setSortType('amount'))}>
        Сумма
        {(sortBy === 'amount') && (<ReactSVG className={styles.ActiveFilterIcon} src={ActiveFilterIcon} />)}
        {(sortBy !== 'amount') && (<ReactSVG className={styles.filterIcon} src={filterIcon} />)}
      </div>
      <div className={styles.sortByIncomeDNC} onClick={() => (setSortType('incomeDNC'))}>
        DNC в день
        {(sortBy === 'incomeDNC') && (<ReactSVG className={styles.ActiveFilterIcon} src={ActiveFilterIcon} />)}
        {(sortBy !== 'incomeDNC') && (<ReactSVG className={styles.filterIcon} src={filterIcon} />)}
      </div>
      <div className={styles.sortByIncomeRZM} onClick={() => (setSortType('incomeRZM'))}>
        RZM в день
        {(sortBy === 'incomeRZM') && (<ReactSVG className={styles.ActiveFilterIcon} src={ActiveFilterIcon} />)}
        {(sortBy !== 'incomeRZM') && (<ReactSVG className={styles.filterIcon} src={filterIcon} />)}
      </div>
      <div className={styles.sortByDate} onClick={() => (setSortType('date'))}>
        Дата покупки
        {(sortBy === 'date') && (<ReactSVG className={styles.ActiveFilterIcon} src={ActiveFilterIcon} />)}
        {(sortBy !== 'date') && (<ReactSVG className={styles.filterIcon} src={filterIcon} />)}
      </div>
    </div>
    <ul className={styles.list}>
      {goodsList && ((showAmountOfContracts(selectedContracts)).map(item => (
        <ContractItem item={item} key={item.id}/>
      ))) }
    </ul>
    <div className={styles.paginationBlock}>
        <Pagination pageCount={count / 5} handlePageClick={handlePageClick}/>
    </div>

  </div>
)};
