import { createSlice } from '@reduxjs/toolkit';

const calendarDaySlice = createSlice({
    name: 'calendarDay',
    initialState: {
      selectedDay: 0,
    },
    reducers: {
      changeDay(state, action) {
        state.selectedDay = action.payload;
      },
    },
});

export const calendarDayReducer = calendarDaySlice.reducer;