import {createSlice} from '@reduxjs/toolkit';
import {getInheritancesListThunk} from './getInheritancesListThunk';
import {GetInheritancesListResponse} from "../../../api/type/settings";

const getInheritancesListSlice = createSlice({
    name: 'getInheritancesList',
    initialState: {
        inheritances: {} as GetInheritancesListResponse,
        loading: false,
        error: false
    },
    reducers: {},
    extraReducers: (builder) => {
        builder.addCase(getInheritancesListThunk.pending, (state) => {
            state.loading = true
        });
        builder.addCase(getInheritancesListThunk.fulfilled, (state, action) => {
            if (action.payload) {
                state.inheritances = action.payload
                state.loading = false
            }
        });
        builder.addCase(getInheritancesListThunk.rejected, (state) => {
            state.loading = false;
            state.error = true
        });
    },
});

export const getInheritancesListReducer = getInheritancesListSlice.reducer;