import {useEffect, useState} from "react"
import {useDispatch, useSelector} from 'react-redux';
import {ReactSVG} from "react-svg";
import {NavBarMobile} from '../navBar/NavBarMobile/NavBarMobile';
import smallLogoIcon from './image/small-logo.svg';
import notificationsIcon from './image/notifications.svg'
import menuIcon from './image/menu.svg'
import calendarIcon from './image/calendar.svg';
import arrowIcon from './image/arrow.svg';
import DNSCoinIcon from '../../../../pages/dashboard/icons/DNCBalance.svg';
import RZMCoinIcon from '../../../../pages/dashboard/icons/RZMBalance.svg';
import vectorIcon from '../../../../pages/dashboard/icons/Vector.svg';
import tetherICon from '../../../../pages/dashboard/icons/Tether.svg';
import cloudIcon from '../../../../pages/dashboard/icons/Cloud.svg';
import clockIcon from '../../../../pages/dashboard/icons/Clock.svg';
import styles from './header.module.scss';
import {AppStateType} from "../../../../core/redux/store";
import {getUserDataThunk} from "../../../../modules/user/getUserData/getUserDataThunk";
import {baseUrl} from "../../../../api/baseRequest";
import {NavLink} from "react-router-dom";

const getMonth = (number: number) => {
    switch (number) {
        case 1:
            return 'Января';
        case 2:
            return 'Февраля';
        case 3:
            return 'Марта';
        case 4:
            return 'Апреля';
        case 5:
            return 'Мая';
        case 6:
            return 'Июня';
        case 7:
            return 'Июля';
        case 8:
            return 'Августа';
        case 9:
            return 'Сентября';
        case 10:
            return 'Октября';
        case 11:
            return 'Ноября';
        case 12:
            return 'Декабря';
    }
}

const days = [
    'Воскресенье',
    'Понедельник',
    'Вторник',
    'Среда',
    'Четверг',
    'Пятница',
    'Суббота'
];

export const Header = (): JSX.Element => {
    const [IsMenuActive, setIsMenuActive] = useState(false);
    const {user} = useSelector((state: AppStateType) => state.user);
    const [fakeCurrentDate, setFakeCurrentDate] = useState(new Date())

    const dispatch = useDispatch()

    const data = new Date().getDate();
    const month = new Date().getMonth();
    const dayOfWeek = new Date().getDay();
    const minutes = new Date().getMinutes();

    useEffect(() => {
        dispatch(getUserDataThunk());
    }, [dispatch])


    useEffect(() => {
        setTimeout(() => setFakeCurrentDate(new Date()), 60000)
    }, [fakeCurrentDate])


    const showMenu = () => {
        setIsMenuActive(true);
    }

    const hideMenu = () => {
        setIsMenuActive(false);
    }

    return (
        <div className={styles.wrapper}>
            <header>
                <div className={styles.leftPanel}>
                    <div className={styles.otherInformation}>
                        <div className={styles.info}>
                            <ReactSVG className={styles.cloudIcon} src={cloudIcon}/>
                            <div className={styles.weather}>
                                {`20`}&#xb0;C
                                <span>{`Облачно`}</span>
                            </div>
                            <ReactSVG className={styles.clockIcon} src={clockIcon}/>
                            <div className={styles.time}>
                                {`${new Date().getHours()}:`} {minutes < 10 ? `0${minutes}` : minutes}
                                <span>{`GMT+3`}</span>
                            </div>
                            <ReactSVG className={styles.calendarIcon} src={calendarIcon}/>
                            <div className={styles.date}>
                                {`${data} ${getMonth(month)}`}
                                <span>{days[dayOfWeek]}</span>
                            </div>
                            <ReactSVG className={styles.arrowToolsIcon} src={arrowIcon}/>
                        </div>
                        <div className={styles.currencyExchange}>
                            <div className={styles.currencyExchangeDNC}>
                                <ReactSVG className={styles.DNSCoinIcon} src={DNSCoinIcon}/>
                                1
                                <ReactSVG className={styles.vectorIcon} src={vectorIcon}/>
                                <ReactSVG className={styles.tetherICon} src={tetherICon}/>
                                1
                                <ReactSVG className={styles.arrowToolsIcon} src={arrowIcon}/>
                            </div>

                            <div className={styles.currencyExchangeRZM}>
                                <ReactSVG className={styles.CoinIcon} src={RZMCoinIcon}/>
                                1
                                <ReactSVG className={styles.vectorIcon} src={vectorIcon}/>
                                <ReactSVG className={styles.tetherICon} src={tetherICon}/>
                                0.10
                                <ReactSVG className={styles.arrowToolsIcon} src={arrowIcon}/>
                            </div>
                        </div>
                    </div>
                </div>

                <div className={styles.rightPanel}>
                    <span className={styles.language}>{`ENG`}</span>
                    <ReactSVG className={styles.notificationsIcon} src={notificationsIcon}/>
                    <div className={styles.profile}>
                        <NavLink to={'/settings/personal-data'}>
                          {user.avatar && (
                            <img
                              className={styles.profilePhoto}
                              src={`${baseUrl}${user.avatar}`}
                              alt="profile_photo"
                            />
                          )}
                            <span>{user?.firstName}</span>
                            <ReactSVG className={styles.arrowIcon} src={arrowIcon}/>
                        </NavLink>
                    </div>
                </div>

                <div className={styles.logo}>
                    <ReactSVG className={styles.smallLogoIcon} src={smallLogoIcon}/>
                </div>

                <div className={styles.navigation}>
                    <ReactSVG className={styles.notificationsIconMobile} src={notificationsIcon}/>
                    <ReactSVG
                        className={styles.menuIcon}
                        src={menuIcon}
                        onClick={() => showMenu()}
                    />
                </div>
            </header>
            {IsMenuActive && (<NavBarMobile hideMenu={hideMenu}/>)}
        </div>
    )
}
