import { createSlice } from '@reduxjs/toolkit';
import { axiosHeadersUpdater } from '../../api/baseRequest';
import { ContractsResponse } from '../../api/type/contracts/contracts';
import { contractsThunk } from './contractsThunk';

const contractsSlice = createSlice({
    name: 'contracts',
    initialState: {
      contracts: {} as ContractsResponse,
      loading: false,
    },
    reducers: {},
    extraReducers: (builder) => {
        builder.addCase(contractsThunk.pending, (state) => {
            state.loading = true;
            axiosHeadersUpdater()
        });
        builder.addCase(contractsThunk.fulfilled, (state, action) => {
            if (action.payload) {
                state.contracts.items = action.payload.items;
                state.contracts.count = action.payload.count;
            }
        });
        builder.addCase(contractsThunk.rejected, (state) => {});
    },

});

export const contractReducer = contractsSlice.reducer;