import { useState } from 'react';
import styles from './news.module.scss';
import { ReactSVG } from 'react-svg';
import arrowIcon from './icons/arrowBack.svg';
import calendarIcon from './icons/calendar.svg';

import { NewsList } from '../dashboard/News/NewsList/NewsList';

interface Props {
  chosenNews: number;
  articles: Article[];
  onClick: () => void;
}

export const News = ({ chosenNews, articles, onClick }: Props): JSX.Element => {
  const [selectedNews, setSelectedNews] = useState(chosenNews);
  const oneNews = articles.find(article => article.id === selectedNews);
  const image = `https://stage-admin.mango.trade${oneNews?.image}`;
  const text = oneNews?.ruText;

  const selectNews = (id: number) => {
    setSelectedNews(id);
  }

  const renderText = () => {
    return {__html: `${text}`};
  }

  return (
      <div className={styles.newsPage}>
        <div className={styles.chosenNews}>
          <div className={styles.headerBlock}>
            <div className={styles.firstBlock}>
              <div className={styles.back} onClick={() => onClick()}>
                <div className={styles.iconBlock}>
                  <ReactSVG className={styles.arrowIcon} src={arrowIcon} />
                </div>
                <p className={styles.arrowIcon}>Назад</p>
              </div>
              <div className={styles.title}>
                {oneNews?.ruTitle}
              </div>
            </div>
            <div className={styles.image}>
              <img src={image} alt="" />
            </div>
          </div>
          <div className={styles.text}
            dangerouslySetInnerHTML={renderText()}
          >
          </div>
          <div className={styles.infoBlock}>
            <div className={styles.lastBack} onClick={() => onClick()}>
              <div className={styles.lastIconBlock}>
                <ReactSVG className={styles.lastArrowIcon} src={arrowIcon} />
              </div>
              <p className={styles.arrowIcon}>Назад</p>
            </div>
            <div className={styles.date}>
            <ReactSVG className={styles.calendarIcon} src={calendarIcon} />
              {oneNews?.createdAt}
            </div>
          </div>
        </div>
        <div className={styles.newsList}>
          <NewsList articles={articles} onClick={selectNews} title={'Вам понравится'} isButton='no'/>
        </div>
      </div>
  )
}