import {createSlice} from '@reduxjs/toolkit';
import {checkSmsCode} from "./checkSmsCodeThunk";

const checkSmsCodeSlice = createSlice({
    name: 'checkSmsCode',
    initialState: {
        phone: '',
        secret: '',
        code: 0,
        loading: false,
        errorCode: false,
    },
    reducers: {},
    extraReducers: (builder) => {
        builder.addCase(checkSmsCode.pending, (state) => {
            state.loading = true;
        });
        builder.addCase(checkSmsCode.fulfilled, (state, action) => {
            if (action.payload) {
                state.loading = false;
                state.secret = action.payload.secretCode
            }
        });
        builder.addCase(checkSmsCode.rejected, (state) => {
            state.loading = false;
            state.errorCode = true;
        });
    }
})

export const checkSmsCodeReducer = checkSmsCodeSlice.reducer;