import style from './security.module.css'
import {FormPasswordForAccount} from './forms/formPasswordForAccount';
import {PasswordForMoneyForm} from "./forms/passwordForMoneyForm";
import {SessionTimeOutForm} from "./forms/sessionTimeOutForm";
import {AuthenticationToggle} from "./forms/authenticationToggle";
import {UpdateUserPasswordRequest} from "../../../../../api/type/user";
import {updateUserPasswordThunk} from "../../../../../modules/user/updateUserPassword/updateUserPasswordThunk";
import {useDispatch} from "react-redux";
import {updateUserFinPasswordThunk} from "../../../../../modules/user/updareFinancialPassword/updateUserFinPasswordThunk";
import {updateTimeSessionThunk} from "../../../../../modules/user/updateTimeSession/updateTimeSessionThunk";

export const Security = (): JSX.Element => {
    const dispatch = useDispatch()

    const onChangePassword = ({new_password, old_password}: UpdateUserPasswordRequest) => {
        dispatch(updateUserPasswordThunk({new_password, old_password}))
    }

    const onChangePasswordForMoney = ({old_password, new_password}: UpdateUserPasswordRequest) => {
        dispatch(updateUserFinPasswordThunk({new_password, old_password}))
    }

    const onChangeTimeOut = (time: number) => {
       dispatch(updateTimeSessionThunk({time: time}))
    }

    const onChangeTwoFactorAuthentication = () => {}

    return (
        <div className={style.security}>
            <div className={`${style.password_block} ${style.wrapperBlock}`}>
                <div className={`${style.password_forAccount} ${style.wrapperItemBlock}`}>
                    <h4>Пароль от аккаунта</h4>
                    <FormPasswordForAccount onChangePassword={onChangePassword}/>
                </div>
                <div className={`${style.password_forMoney} ${style.wrapperItemBlock}`}>
                    <h4>Финансовый пароль</h4>
                    <PasswordForMoneyForm onChangePassword={onChangePasswordForMoney}/>
                </div>
            </div>
            <div className={`${style.sessionTime_block} ${style.wrapperBlock}`}>
                <div className={`${style.session_timeOut} ${style.wrapperItemBlock}`}>
                    <h4>Время сессии</h4>
                    <SessionTimeOutForm onSubmitHandle={onChangeTimeOut}/>
                </div>
                <div className={`${style.wrapperItemBlock}`}>
                    <h4>Двухэтапная аутентификация </h4>
                    <AuthenticationToggle onChange={onChangeTwoFactorAuthentication}/>
                </div>
            </div>
        </div>
    )
}