import {Redirect} from "react-router-dom";
import {TopBlock} from '../../ui/topBlock/topBlock'
import style from './recovery.module.css'
import {ArrowBack} from "../../ui/buttons/arrowBack/arrowBack";
import {ForgotPassword} from "./forgotPassword/forgotPassword";

export const Recovery = (): JSX.Element => {

    const redirectToBack = () => <Redirect to="login" />

    return (
        <div className={`${style.recoveryWrapper}`}>
            <div className={`${style.recovery}`}>
                <TopBlock/>
                <ArrowBack linkToBack={'login'} onClick={ () => redirectToBack } />

                <ForgotPassword />
            </div>
        </div>
    )
}