import {useForm} from "react-hook-form";
import {InputCheckBox} from "../../../../ui/inputs/inputCheckBox/inputCheckBox";
import {PropsForm} from "./registrationForms";
import style from "../../registration.module.css";
import {FooterForm} from "./footerForm";
import {useDispatch, useSelector} from "react-redux";
import {AppStateType} from "../../../../core/redux/store";
import {NavLink, Redirect} from "react-router-dom";
import {registration} from "../../../../modules/registration/registrationThunk";
import {RegistrationFormData5} from "../../../../api/type/registration";
import {FormHeader} from "./formHeader";

export const RegistrationForm5 = ({toggleActiveStep}: PropsForm): JSX.Element => {
    const {
        email, country, referral, password, isRegistered
    } = useSelector((state: AppStateType) => state.registration)
    const dispatch = useDispatch();

    const {secret} = useSelector((state: AppStateType) => state.checkSmsCode)

    const {handleSubmit, control, formState: {errors}} = useForm<RegistrationFormData5>({
        defaultValues: {
            policy1: false,
            policy2: false,
            policy3: false,
            policy4: false,
            policy5: false,
        },
        mode: 'onChange',
    });

    const onSubmit = (data: RegistrationFormData5) => {
        const {policy1, policy2, policy3, policy4, policy5} = data

        if (policy1 && policy2 && policy3 && policy4 && policy5) {
            const requestData = {secret, email, country, referral, password}

            dispatch(registration(requestData))
        }
    }

    if (isRegistered) {
        return <Redirect to={'login'}/>
    }

    return (
        <>
            <FormHeader title={'Соглашение'}
                        description={'Прочитайте документы и подтвердите, что вы соглашаетесь со всеми условиями'}/>

            <form onSubmit={handleSubmit(onSubmit)}>
                <div className={`${style.footerFormCheckBox} ${errors.policy1 ? style.checked : ''}`}>
                    <InputCheckBox control={control} name='policy1' rules={{required: true}}/>
                    <label htmlFor="policy1">Я прочитал и согласен с
                        <NavLink to={'#'}> политикой конфиденциальности </NavLink>
                    </label>
                </div>

                <div className={`${style.footerFormCheckBox} ${errors.policy2 ? style.checked : ''}`}>
                    <InputCheckBox control={control} name='policy2' rules={{required: true}}/>
                    <label htmlFor="policy2">Я прочитал и согласен с
                        <NavLink to={'#'}> пользовательским соглашением </NavLink>
                    </label>
                </div>

                <div className={`${style.footerFormCheckBox} ${errors.policy3 ? style.checked : ''}`}>
                    <InputCheckBox control={control} name='policy3' rules={{required: true}}/>
                    <label htmlFor="policy3">Я прочитал и согласен с использованием
                        <NavLink to={'#'}> файлов cookie </NavLink>
                    </label>
                </div>

                <div className={`${style.footerFormCheckBox} ${errors.policy4 ? style.checked : ''}`}>
                    <InputCheckBox control={control} name='policy4' rules={{required: true}}/>
                    <label htmlFor="policy4">Я прочитал и согласен с
                        <NavLink to={'#'}> политикой риска </NavLink>
                    </label>
                </div>

                <div className={`${style.footerFormCheckBox} ${errors.policy5 ? style.checked : ''}`}>
                    <InputCheckBox control={control} name='policy5' rules={{required: true}}/>
                    <label htmlFor="policy5">Согласиться со всеми условиями</label>
                </div>

                <FooterForm toggleActiveStep={toggleActiveStep} stepPrev={3}>
                    Завершить
                </FooterForm>

            </form>
        </>
    )
}