import style from "./buttonFilterSchedule.module.css";
import {ReactSVG} from "react-svg";
import iconFilter from "../../../assets/icons/iconFilter.svg";

interface Props {
    onClick: () => void
}

export const ButtonFilterSchedule = ({onClick}: Props): JSX.Element => {

  return (
      <button onClick={onClick} className={style.filter}>
          <ReactSVG src={iconFilter}/>
          <span>Фильтр</span>
      </button>
  )
}