import {createSlice} from '@reduxjs/toolkit';
import {PartnersStatistic} from "../../../api/type/structure";
import {partnersStatisticThunk} from './partnersStatisticThunk';

const partnersStatisticSlice = createSlice({
    name: 'partnersStatistic',
    initialState: {
        partners: {} as PartnersStatistic,
        error: false,
        loading: false
    },
    reducers: {},
    extraReducers: (builder) => {

        builder.addCase(partnersStatisticThunk.pending, (state) => {
            state.loading = true;
        });
        builder.addCase(partnersStatisticThunk.fulfilled, (state, action) => {
            if (action.payload) {
                state.partners = action.payload
                state.loading = false
            }
        });
        builder.addCase(partnersStatisticThunk.rejected, (state) => {
            state.loading = false;
            state.error = true;
        });
    },

});

export const structurePartnersReducer = partnersStatisticSlice.reducer;