import {ReactSVG} from "react-svg";
import style from './butonSettingsSchedule.module.css';

interface Props {
  onClick?: () => void,
  icon: string,
  title: string
}

export const ButtonSettingsSchedule = ({onClick, icon, title}: Props): JSX.Element => {

  return (
      <button className={style.btn} onClick={() => onClick}>
        <ReactSVG src={icon} />
        <span>{title}</span>
      </button>
  )
}
