import styles from './TakeOutButton.module.scss';
import {FC} from "react";

interface Props {
    onClick?: () => void;
    color: 'green' | 'gold' | 'brown' | 'blue';
}

export const TakeOutButton: FC<Props> = (props): JSX.Element => {
    const {color, children, onClick} = props

    return (
        <button onClick={onClick} className={`${styles.button} ${styles[color]}`}>
            <span>{children}</span>
        </button>
    )
}

