import {ReactNode} from "react";
import {ReactSVG} from "react-svg";
import style from './myLevelCardBody.module.css'
import iconInfo from '../../../../assets/icons/iconInfo.svg'

interface Props {
    widthBlock: string | ReactNode,
    title: string | ReactNode,
    subTitle: string | ReactNode,
    currentValue: string | ReactNode,
}

export const MyLevelCardBody = (props: Props): JSX.Element => {
    const {widthBlock, title, subTitle, currentValue} = props

    return (
        <div className={style.myLevelCardBody}>
            <div style={{width: `${widthBlock}%`}}
                 className={ `${style.myLevelCardBody_leftBlock} ${widthBlock === '100' ? style.border : ''}`}>
                <span className={style.title}>{title}
                    <ReactSVG className={style.iconInfo} src={iconInfo}/>
                </span>
                <span className={style.subTitle}>{subTitle}</span>
            </div>

            <div className={style.myLevelCardBody_rightBlock}>
                <span>{currentValue}</span>
            </div>
        </div>
    )
}