import {useForm} from "react-hook-form";
import {ButtonEnter} from "../../../../ui/buttons/buttonEnter/buttonEnter";
import {ErrorsMessage} from "../../../../ui/errorsMessage/errorsMessage";
import {InputPassword} from "../../../../ui/inputs/inputPassword/inputPassword";
import stranger from "../../../authorization/authorization.module.css";
import style from "../../registration.module.css";
import {PropsForm} from "./registrationForms";
import {useDispatch, useSelector} from "react-redux";
import {AppStateType} from "../../../../core/redux/store";
import {onChangeForm4} from "../../../../modules/registration/registrationSlice";
import {useEffect, useState} from "react";
import {FormHeader} from "./formHeader";

export const RegistrationForm4 = ({toggleActiveStep}: PropsForm): JSX.Element => {
    const {password} = useSelector((state: AppStateType) => state.registration)
    const {errorCode} = useSelector((state: AppStateType) => state.checkSmsCode)
    const [passIsMath, setPassIsMath] = useState(false)
    const dispatch = useDispatch();

    const {handleSubmit, control, formState: {errors}} = useForm<{ password: string, repeatPassword: string }>({
        defaultValues: {
            password: password,
            repeatPassword: '',
        },
        mode: 'onChange',
    });

    useEffect( () => {
        if(errorCode) {
            toggleActiveStep(2)
        }
    }, [toggleActiveStep, errorCode] )

    const onSubmit = (data: { password: string, repeatPassword: string }) => {
        if (data.password === data.repeatPassword) {
            dispatch(onChangeForm4({password: data.password}))
            toggleActiveStep(4)
        } else {
            setPassIsMath(prev => !prev)
        }
    }

    return (
        <>
            <FormHeader title={'Безопасность'}
                        description={'Придумайте пароль и подтвердите его. Пароль должен содержать символы — Аа, 123, @#$'}/>

            <form onSubmit={handleSubmit(onSubmit)}>
                <div className={`${stranger.formItem}`}>
                    <label htmlFor="password">Новый пароль</label>
                    <InputPassword control={control} name='password' rules={{required: true}}/>
                    {errors.password &&
                    <ErrorsMessage textMessage={''}/>}
                </div>

                <div className={`${stranger.formItem}`}>
                    <label htmlFor="repeatPassword">Новый пароль</label>
                    <InputPassword control={control} name='repeatPassword' rules={{required: true}}/>
                    {errors.repeatPassword &&
                    (<ErrorsMessage textMessage={''}/>)}
                </div>

                {passIsMath && (<ErrorsMessage textMessage={'Пароли не совпадают'}/>)}

                <div className={`${stranger.footerForm} ${style.footerFormFlex}`}>
                    <button className={style.backBtn} onClick={() => toggleActiveStep(2)}>Назад</button>
                    <ButtonEnter value={'Продолжить'}/>
                </div>
            </form>
        </>
    )
}