import { createSlice } from '@reduxjs/toolkit';
import { axiosHeadersUpdater } from '../../api/baseRequest';
import { PromoListResponse } from '../../api/type//promo/promoList';
import { promoListThunk } from './promoListThunk';

const promoListSlice = createSlice({
    name: 'promoList',
    initialState: {
      promoList: {} as PromoListResponse,
      loading: false,
    },
    reducers: {},
    extraReducers: (builder) => {
        builder.addCase(promoListThunk.pending, (state) => {
            state.loading = true;
            axiosHeadersUpdater()
        });
        builder.addCase(promoListThunk.fulfilled, (state, action) => {
            if (action.payload) {
                state.promoList = action.payload;
            }
        });
        builder.addCase(promoListThunk.rejected, (state) => {});
    },

});

export const promoListReducer = promoListSlice.reducer;