import {Layout} from "../../ui/layout/layout"
import { useEffect } from 'react';
import styles from './promo.module.scss';
import { ReactSVG } from 'react-svg';
import calendarIcon from '../../ui/layout/components/header/image/calendar.svg';
import marketIcon from './icons/marketing.svg';
import { AppStateType } from "../../core/redux/store";
import { useDispatch, useSelector } from "react-redux";
import { promoListThunk } from "../../modules/promo/promoListThunk";
import Button from '../../ui/buttons/BuyButton/BuyButton';


const getMonth = (number: string) => {
  switch (number) {
      case '01':
          return 'января';
      case '02':
          return 'февраля';
      case '03':
          return 'марта';
      case '04':
          return 'апреля';
      case '05':
          return 'мая';
      case '06':
          return 'июня';
      case '07':
          return 'июля';
      case '08':
          return 'августа';
      case '09':
          return 'сентября';
      case '10':
          return 'октября';
      case '11':
          return 'ноября';
      case '12':
          return 'декабря';
  }
}

export const Promo = (): JSX.Element => {
  const promoList = useSelector((state: AppStateType) => state.promoList.promoList.items);
  const dispatch = useDispatch()
  const BASE_URL = 'https://stage-admin.mango.trade';
  let firstPromo;

  if (promoList) {
    firstPromo = promoList[0];
  }

  useEffect(() => {
      dispatch(promoListThunk({
        limit: 100,
        offset: 0,
      }));
  }, [dispatch])

    return (
        <Layout>
          <div className={styles.promoBlock}>
            {firstPromo && (
              <>
                <div className={` ${styles.firstItem}`}>
                  <div className={styles.promoImageBlock}>
                    <img src={`${BASE_URL}${firstPromo.image}`} alt="promo" />
                    <div className={styles.promoDate}>
                      <ReactSVG className={styles.calendarIcon} src={calendarIcon} />
                      {`${firstPromo.date.slice(8, 10)} ${getMonth(firstPromo.date.slice(5, 7))}`}
                    </div>
                    <div className={styles.promoContent}>
                      <h2 className={styles.promoTitle}>{firstPromo.ruTitle}</h2>
                      <p className={styles.promoText}>{firstPromo.ruDescription}</p>
                    </div>
                  </div>
                </div>
                <div className={styles.promoMobileContent}>
                  <h2 className={styles.promoTitle}>{firstPromo.ruTitle}</h2>
                  <p className={styles.promoText}>{firstPromo.ruDescription}</p>
                </div>
              </>)}

            <h2 className={styles.marketingTitle}>Маркетинг</h2>
            <div className={styles.marketing}>
              <div className={styles.logoBlock}>
                <div className={styles.iconBlock}>
                  <ReactSVG className={styles.marketIcon} src={marketIcon} />
                </div>
                <div className={styles.contentBlock}>
                  <p>Логотип</p>
                  <div className={styles.buttonsBlock}>
                    <div className={styles.button}>
                      <Button color='blue' onClick={() => {}}>
                        SVG
                      </Button>
                    </div>
                    <div className={styles.button}>
                      <Button color='blue' onClick={() => {}}>
                        PNG
                      </Button>
                    </div>
                  </div>
                </div>
              </div>

              <div className={styles.logoBlock}>
                <div className={styles.iconBlock}>
                    <ReactSVG className={styles.marketIcon} src={marketIcon} />
                </div>
                <div className={styles.contentBlock}>
                  <p>Презентация</p>
                  <div className={styles.buttonsBlock}>
                    <div className={styles.button}>
                      <Button color='blue' onClick={() => {}}>
                        Скачать
                      </Button>
                    </div>
                  </div>
                </div>
              </div>
              
              <div className={styles.logoBlock}>
                <div className={styles.iconBlock}>
                  <ReactSVG className={styles.marketIcon} src={marketIcon} />
                </div>
                <div className={styles.contentBlock}>
                  <p>Брендбук</p>
                  <div className={styles.buttonsBlock}>
                    <div className={styles.button}>
                      <Button color='blue' onClick={() => {}}>
                        Скачать
                      </Button>
                    </div>
                  </div>
                </div>
              </div>
            </div>

            <ul className={styles.promoList}>
            {promoList && (
              promoList.map(item => (
                <li className={styles.promoItem} key={item.id}>
                  <div className={styles.promoImageBlock}>
                    <img src={`${BASE_URL}${item.image}`} alt="promo" />
                    <div className={styles.promoDate}>
                      <ReactSVG className={styles.calendarIcon} src={calendarIcon} />
                      {`${item.date.slice(8, 10)} ${getMonth(item.date.slice(5, 7))}`}
                    </div>
                    <div className={styles.promoContent}>
                      <h2 className={styles.promoTitle}>{item.ruTitle}</h2>
                      <p className={styles.promoText}>{item.ruDescription}</p>
                    </div>
                  </div>
                  <div className={styles.promoMobileContent}>
                    <h2 className={styles.promoTitle}>{item.ruTitle}</h2>
                    <p className={styles.promoText}>{item.ruDescription}</p>
                  </div>
                </li>
              ))
            )}
            </ul>
          </div>
        </Layout>
    )
}