import {useForm} from "react-hook-form";
import {useDispatch, useSelector} from "react-redux";
import stranger from "../../../authorization/authorization.module.css";
import {ErrorsMessage} from "../../../../ui/errorsMessage/errorsMessage";
import {Input} from "../../../../ui/inputs/input/input";
import {RegistrationFormData2} from "../../../../api/type/registration";
import {AppStateType} from "../../../../core/redux/store";
import {onChangeForm2} from "../../../../modules/registration/registrationSlice";
import {SelectCountries} from "../../../../ui/selectCountries/selectCountries";
import {sendSmsCode} from "../../../../modules/registration/sendSmsCode/sendSmsCodeThunk";
import {PropsForm} from "./registrationForms";
import {FooterForm} from "./footerForm";
import {FormHeader} from "./formHeader";

export const RegistrationForm2 = ({toggleActiveStep}: PropsForm): JSX.Element => {
    const {country, email, phone} = useSelector((state: AppStateType) => state.registration)
    const {error} = useSelector((state: AppStateType) => state.sendSmsCode)
    const dispatch = useDispatch();

    const {handleSubmit, control, formState: {errors}} = useForm<RegistrationFormData2>({
        defaultValues: {
            country: country,
            email: email,
            phone: phone,
        },
        mode: 'onChange',
    });


    const onSubmit = (data: RegistrationFormData2) => {
        dispatch(onChangeForm2(data))
        dispatch(sendSmsCode(data.phone))

        toggleActiveStep(2)
    }

    const customStyles = {
        control: () => ({
            color: 'white',
            backgroundColor: 'transparent',
            display: 'flex',
            padding: '17px 0',
            borderBottom: 'solid 1px rgba(255, 255, 255, 0.2)'
        }),
        input: () => ({
            color: 'white'
        })
    }


    const selectLocation = (value: string) => {
        console.log(value)
    }

    return (
        <>
            <FormHeader title={'Контактные данные'} description={'Где вы живете, ваш e-mail и телефон'}/>

            <form onSubmit={handleSubmit(onSubmit)}>
                <div className={`${stranger.formItem}`}>
                    {error && (<ErrorsMessage textMessage={'Глобальная ошибка'}/>)}
                    <SelectCountries onSelectCountries={selectLocation} customStyles={customStyles}/>
                    {errors.country &&
                    (<ErrorsMessage textMessage={'Выберите город'}/>)}
                </div>

                <div className={`${stranger.formItem}`}>
                    <div className={`${stranger.formItem_pass}`}>
                        {error ? <ErrorsMessage textMessage={'Глобальная ошибка'}/> :
                            <label htmlFor="E-mail">E-mail</label>}
                        <Input control={control} name='email' rules={{required: true}}/>
                        {errors.email &&
                        <ErrorsMessage textMessage={'E-mail введен некорректно'}/>}
                    </div>
                </div>

                <div className={`${stranger.formItem}`}>
                    <div className={`${stranger.formItem_pass}`}>
                        {error ? <ErrorsMessage textMessage={'Этот номер уже используется'}/> :
                            <label htmlFor="phone">Телефон</label>}
                        <Input control={control} name='phone' rules={{required: true}}/>
                        {errors.phone &&
                        <ErrorsMessage textMessage={'Телефон введен некорректно'}/>}
                    </div>
                </div>

                <FooterForm stepPrev={0} toggleActiveStep={toggleActiveStep}>
                    Продолжить
                </FooterForm>

            </form>
        </>
    )
}