import {NavLink} from "react-router-dom";
import {ReactSVG} from "react-svg";
import style from './settingsMenu.module.css'
import iconUser from "../../icon/iconUser.svg";
import iconSecurity from "../../icon/iconSecurity.svg";
import iconSocialNetwork from "../../icon/iconSocialNetwork.svg";
import iconNotifications from "../../icon/iconNotifications.svg";
import iconDocuments from "../../icon/iconDocumets.svg";
import iconInheritance from "../../icon/iconInheritance.svg";
import iconSupport from "../../icon/iconSupport.svg";

const settingsMenu = [
    {
        value: 'Личные данные',
        icon: iconUser,
        src: 'personal-data'
    },
    {
        value: 'Безопасность',
        icon: iconSecurity,
        src: 'security'
    },
    {
        value: 'Соцсети',
        icon: iconSocialNetwork,
        src: 'social-network'
    },
    {
        value: 'Уведомления',
        icon: iconNotifications,
        src: 'notifications'
    },
    {
        value: 'Документы',
        icon: iconDocuments,
        src: 'documents'
    },
    {
        value: 'Наследство',
        icon: iconInheritance,
        src: 'inheritance'
    },
    {
        value: 'Поддержка',
        icon: iconSupport,
        src: 'support'
    },
]

interface SettingsMenuProps {
    onSelectItemMenu: (data: {value: string, icon: string}) => void
}

export const SettingsMenu = ({onSelectItemMenu}: SettingsMenuProps): JSX.Element => {

    return (
        <div className={`${style.settingsMenu} ${style.hidden}`}>
            <ul className={style.settingsMenuItem}>
                {settingsMenu.map((item) => (
                    <SettingsMenuItem onSelectItemMenu={onSelectItemMenu} key={item.value} {...item} />
                ))}
            </ul>
        </div>
    )
}

interface Props {
    icon: string,
    value: string,
    src: string,
    onSelectItemMenu: (data: {value: string, icon: string}) => void
}

const SettingsMenuItem = ({icon, value, src, onSelectItemMenu}: Props): JSX.Element => {
    return (
        <li className={style.item}>
            <NavLink onClick={() => onSelectItemMenu({value, icon})} activeClassName={style.active} to={`/settings/${src}`}>
                <ReactSVG className={style.icon} src={icon}/>
                <span className={style.text}>{value}</span>
            </NavLink>
        </li>
    )
}