import {ReactSVG} from "react-svg";
import styles from "./deleteContact.module.css";
import rejectContractIcon from "../../../pages/contacts/icons/XBox.svg";

export interface Props {
    onClick?: () => void
}

export const DeleteContact = ({onClick}: Props): JSX.Element => {
    return (
        <button className={styles.rejectBox} onClick={onClick}>
            <ReactSVG className={styles.rejectContractIcon} src={rejectContractIcon}/>
        </button>
    )
}