import {createAsyncThunk} from '@reduxjs/toolkit';
import {baseRequestAxios} from '../../../api/baseRequest';
import {PartnersStatistic} from "../../../api/type/structure";

export const partnersStatisticThunk = createAsyncThunk(
    'structurePartnersStatistic',
    async (_, {rejectWithValue}) => {
        try {
            const response = await baseRequestAxios.get<PartnersStatistic>("/api/structure/partners-statistic")
            if (response.status === 200) {
                return response.data
            }
        } catch (errors) {
            return rejectWithValue(errors)
        }
    }
)