import { createAsyncThunk } from '@reduxjs/toolkit';
import { baseRequestAxios } from '../../../api/baseRequest';
import { RejectContractRequest, RejectContractResponse } from '../../../api/type/contracts/rejectContract';

export const rejectContractThunk = createAsyncThunk(
  'rejectContract',
  async (requestData: RejectContractRequest, {rejectWithValue}) => {
    try {
        const response  = await baseRequestAxios.post<RejectContractResponse>("/api/contract/close", {
          contract_id: requestData.contract_id,
        })
        if (response.status === 200) {
            return response.data
        }
    } catch (errors){
        return  rejectWithValue(errors)
    }
  }
)
