import styles from './MainContractPage.module.scss';
import {useEffect, useState} from "react"

import { SmartContract } from '../contractsType/smart/smartContract';
import { GoldContract } from '../contractsType/gold/goldContract';
import { MangoContract } from '../contractsType/mango/mangoContract';
import { Info } from '../info/info';
import { ChooseContract } from '../ChooseContract/chooseContract';
import { useDispatch, useSelector } from 'react-redux';
import { AppStateType } from '../../../core/redux/store';
import { contractsThunk } from '../../../modules/contracts/contractsThunk';


export const MainContractPage = () => {
  const [isInfoActive, setIsInfoActive] = useState(true);
  const { items } = useSelector((state: AppStateType) => state.contracts.contracts);
  const dispatch = useDispatch()

  useEffect(() => {
    dispatch(contractsThunk({
      contract_type: 3,
      limit: 10000,
      offset: 0,
    }));
  }, [dispatch])
  
  const hideInfo = () => {
    setIsInfoActive(false);
  }

  return (
    <div className={styles.ContractsPage}>
      {isInfoActive && ( <Info onClick={hideInfo} /> )}

      <ChooseContract items={items} />

      <h2 className={styles.title}>Все Контракты</h2>
      <div className={styles.contractsBlock}>
        <div className={styles.contractItem}>
          <SmartContract />
        </div>
        <div className={styles.contractItem}>
          <GoldContract />
        </div>
        <div className={styles.contractItem}>
          <MangoContract />
        </div>
      </div>
    </div>
  )
}
