import {Path, useController, UseControllerProps} from "react-hook-form";
import style from './inputCheckBox.module.css'

export function InputCheckBox<T>(props: UseControllerProps<T, Path<T>>): JSX.Element {
    const {field} = useController(props);
    return (
        <div className={style.checkBoxWrapper}>
            <input checked={field.value} id={field.name} className={style.checkBox} {...field} type='checkbox'/>
        </div>
    );
}