import {ReactSVG} from "react-svg";
import {Layout} from "../../ui/layout/layout"
import iconPlay from "../../assets/icons/iconPlay.svg";
import {DNCBalance} from "../dashboard/DNCBalance/DNCBalance";
import {TotalContracts} from "../dashboard/TotalContracts/TotalContractsSample/TotalContracts";
import {MyLevelCard} from "./componets/mylevelCard/mylevelCard";
import {ReferralLink} from "./componets/reffelalLink/reffelalLink";
import style from './structure.module.css';
import iconClock from '../../assets/icons/iconClock.svg';
import iconFlag from '../../assets/icons/iconFlag.svg';
import iconBox from '../../assets/icons/iconBox.svg';
import iconlightning2 from '../../assets/icons/iconlightning2.svg';
import partners from '../../pages/dashboard/icons/partners.svg';
import referralProfit from '../../pages/dashboard/icons/referralProfit.svg';
import {StructureItem} from "./componets/structureItem/structureItem";
import {Schedule} from "./componets/schedule/schedule";
import {ButtonFilterDay} from "../../ui/buttons/buttonFilterDay/buttonsFilterDay";
import {useDispatch, useSelector} from "react-redux";
import {useEffect} from "react";
import {structurePartnersThunk} from "../../modules/structure/partners/structurePartnersThunk";
import {AppStateType} from "../../core/redux/store";
import {structureBonuses} from "../../modules/structure/bonuses/structureBonusesThunk";
import {structureLines} from "../../modules/structure/lines/structureLinesThunk";
import {structureBonusesTransactions} from "../../modules/structure/bonusesTransactions/bonusesTransactionsThunk";
import {nextLevelBlock} from "../../modules/structure/nextLevelBlock/nextLevelBlockThunk";
import {partnersStatisticThunk} from "../../modules/structure/partnersStatistic/partnersStatisticThunk";

export const Structure = (): JSX.Element => {
    const {lines} = useSelector((state: AppStateType) => state.structureLines)
    const dispatch = useDispatch()

    useEffect(() => {
        dispatch(structureLines())
        dispatch(structureBonuses())
        dispatch(nextLevelBlock())
        dispatch(partnersStatisticThunk())
        dispatch(structurePartnersThunk({limit: 1, offset: 1}))
        dispatch(structureBonusesTransactions({limit: 1, offset: 1}))
    }, [dispatch])

    return (
        <Layout>
            <div className={style.structure}>
                <div className={style.structureWrapper}>
                    <div className={style.structureTopBlock}>
                        <div className={style.rightBlockItem}>
                            <div className={style.rightBlock}>
                                <DNCBalance balance={1235513.32} title={'Реферальные бонсы'} income={'308'}>
                                    <div className={style.footerCardBtn}>
                                        <button>
                                            <ReactSVG src={iconPlay}/>
                                            Как это работает?
                                        </button>
                                    </div>
                                </DNCBalance>
                                <ReferralLink/>
                            </div>
                            <div className={style.leftBlock}>
                                <MyLevelCard/>
                            </div>
                        </div>
                        <div className={style.leftBlockItem}>
                            <div className={style.item}>
                                <TotalContracts sum={58000} icon={iconClock}/>
                                <TotalContracts sum={356} icon={partners}/>
                            </div>
                            <div className={style.item}>
                                <TotalContracts sum={58000} icon={iconFlag}/>
                                <TotalContracts sum={58000} icon={iconBox}/>
                            </div>
                            <div className={style.item}>
                                <TotalContracts sum={58000} icon={referralProfit}/>
                                <TotalContracts sum={58000} icon={iconlightning2}/>
                            </div>
                        </div>
                    </div>

                    <div className={style.structureBottomBlock}>
                        <div className={style.rightBlockItem}>
                           <StructureItem lines={lines.lines}/>
                        </div>
                        <div className={style.leftBlockItem}>
                            <Schedule/>
                            <div className={style.buttonsBlock}>
                                <ButtonFilterDay>День</ButtonFilterDay>
                                <ButtonFilterDay>Месяц</ButtonFilterDay>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </Layout>
    )
}

