import {FC, ReactNode} from "react";
import style from './buttonEnter.module.css'

interface Props {
    onClick?: () => void
    value?: string | ReactNode,
    disabled?: boolean,
}

export const ButtonEnter: FC<Props>  = ({value, disabled, children}): JSX.Element => {

    return (
        <button disabled={disabled} className={`${style.btn} ${disabled? style.btn_disabled : ''}`}>{children}{value}</button>
    )
}