import {useForm} from "react-hook-form";
import {Input} from "../../../../../ui/inputs/input/input";
import {TakeOutButton} from "../../../../../ui/buttons/TakeOutButton/TakeOutButton";
import {useDispatch, useSelector} from "react-redux";
import {AppStateType} from "../../../../../core/redux/store";
import {SocialNetworkFormProps} from "../../../../../api/type/user";
import {updateSocialNetwork} from "../../../../../modules/user/getUserData/getUserDataSlice";
import {FormItem} from "../../formItem/formItem";
import style from './socialNetwork.module.css'

export const SocialNetwork = (): JSX.Element => {
    const {facebook, instagram, telegram, twitter} = useSelector((state: AppStateType) => state.user.user)
    const dispatch = useDispatch()
    const {handleSubmit, control} = useForm<SocialNetworkFormProps>({
        defaultValues: {
            facebook: facebook,
            instagram: instagram,
            twitter: twitter,
            telegram: telegram,
        },
        mode: 'onChange',
    });

    const onSubmit = (data: SocialNetworkFormProps) => {
        dispatch(updateSocialNetwork(data))
    }

    return (
        <form className={style.socialNetwork} onSubmit={handleSubmit(onSubmit)}>
            <div className={style.socialNetworkForm}>
                <div className={style.wrapperBlock}>
                    <FormItem name='facebook' label='Facebook'>
                        <Input<SocialNetworkFormProps> control={control} name={'facebook'} rules={{required: true}}/>
                    </FormItem>

                    <FormItem name='instagram' label='Instagram'>
                        <Input<SocialNetworkFormProps> control={control} name={'instagram'} rules={{required: true}}/>
                    </FormItem>
                </div>
                <div className={style.wrapperBlock}>
                    <FormItem name='twitter' label='Twitter'>
                        <Input<SocialNetworkFormProps> control={control} name={'twitter'} rules={{required: true}}/>
                    </FormItem>

                    <FormItem name='telegram' label='Telegram'>
                        <Input<SocialNetworkFormProps> control={control} name={'telegram'} rules={{required: true}}/>
                    </FormItem>
                </div>
            </div>
            <TakeOutButton color={'blue'}>
                сохранить
            </TakeOutButton>
        </form>
    )
}