import {useDispatch, useSelector} from "react-redux";
import {useForm} from "react-hook-form";
import {FormItem} from "../formItem";
import {SwitchBlock} from "../../../../../../ui/switchBlock/switchBlock";
import {TakeOutButton} from "../../../../../../ui/buttons/TakeOutButton/TakeOutButton";
import {NotificationsFormTgProps} from "../../../../../../api/type/user";
import {AppStateType} from "../../../../../../core/redux/store";
import {toggleNotificationsTgForm} from "../../../../../../modules/user/getUserData/getUserDataSlice";
import style from "../notifications.module.css";

interface Props {
    onSubmitHandle: (data: NotificationsFormTgProps) => void
}

export const FormTelegram = ({onSubmitHandle}: Props): JSX.Element => {
    const {onIncomeTg, onContractIncomeTg, onSuccessWithdrawTg, onStartBonusTg,
        onNewItemsInCalendarTg, onEventTg, onCourseTg, onWebinarsTg, onNewsTg
    } = useSelector((state: AppStateType) => state.user.user)

    const dispatch = useDispatch()

    const {handleSubmit, control} = useForm<NotificationsFormTgProps>({
        defaultValues: {
            onIncomeTg: onIncomeTg,
            onContractIncomeTg: onContractIncomeTg,
            onSuccessWithdrawTg: onSuccessWithdrawTg,
            onStartBonusTg: onStartBonusTg,
            onEventTg: onEventTg,
            onCourseTg: onCourseTg,
            onNewItemsInCalendarTg: onNewItemsInCalendarTg,
            onWebinarsTg: onWebinarsTg,
            onNewsTg: onNewsTg,
        },
        mode: 'onChange',
    });

    const onSubmit = (data: NotificationsFormTgProps) => {
        onSubmitHandle(data);
    }

    const onChangeNotifications = (value: boolean) => {
        dispatch(toggleNotificationsTgForm({value: value}))
    }

    return (
        <form onSubmit={handleSubmit(onSubmit)}>
            <SwitchBlock activeValue={'Включить'} passiveValue={'Выключить'} onChange={onChangeNotifications}/>
            <div className={style.formItem_wrapper}>
                <div>
                    <FormItem<NotificationsFormTgProps> name={'onNewsTg'} control={control} label={'Новости'}/>
                    <FormItem<NotificationsFormTgProps> name={'onWebinarsTg'} control={control} label={'Вебинары'}/>
                    <FormItem<NotificationsFormTgProps> name={'onCourseTg'} control={control} label={'Курсы'}/>
                    <FormItem<NotificationsFormTgProps> name={'onEventTg'} control={control} label={'События'}/>
                    <FormItem<NotificationsFormTgProps> name={'onNewItemsInCalendarTg'} control={control}
                                                        label={'Новые отметки в календаре'}/>
                </div>
                <div>
                    <FormItem<NotificationsFormTgProps> name={'onStartBonusTg'} control={control}
                                                        label={'Успешное снятие средств'}/>
                    <FormItem<NotificationsFormTgProps> name={'onIncomeTg'} control={control}
                                                        label={'Пополнение счета'}/>
                    <FormItem<NotificationsFormTgProps> name={'onIncomeTg'} control={control}
                                                        label={'Реферальные начисления'}/>
                    <FormItem<NotificationsFormTgProps> name={'onContractIncomeTg'} control={control}
                                                        label={'Начисления по контрактам'}/>
                </div>
            </div>
            <TakeOutButton color={'blue'}>
                сохранить
            </TakeOutButton>
        </form>
    )
}