import { createSlice } from '@reduxjs/toolkit';
import { axiosHeadersUpdater } from '../../api/baseRequest';
import { OneNewsResponse } from '../../api/type/oneNews';
import { oneNewsThunk } from './oneNewsThunk';

const oneNewsSlice = createSlice({
    name: 'oneNews',
    initialState: {
      oneNews: {} as OneNewsResponse,
      loading: false,
    },
    reducers: {},
    extraReducers: (builder) => {
        builder.addCase(oneNewsThunk.pending, (state) => {
            state.loading = true;
            axiosHeadersUpdater()
        });
        builder.addCase(oneNewsThunk.fulfilled, (state, action) => {
            if (action.payload) {
                state.oneNews = action.payload;
            }
        });
        builder.addCase(oneNewsThunk.rejected, (state) => {});
    },

});

export const oneNewsReducer = oneNewsSlice.reducer;