import {NavLink} from 'react-router-dom'
import style from './arrowBack.module.css'

interface Props {
    onClick: () => void,
    linkToBack?: string
}

export const ArrowBack = ({onClick, linkToBack}: Props): JSX.Element => {
    return (
        <NavLink to={linkToBack ? linkToBack : '#'} className={style.backBtn} onClick={() => onClick()}>Назад</NavLink>
    )
}