import { createAsyncThunk } from '@reduxjs/toolkit';
import { baseRequestAxios } from '../../api/baseRequest';
import { transactionsResponse, transactionsRequest } from '../../api/type/contracts/contract.transaction';

export const transactionsThunk = createAsyncThunk(
  'transactions',
  async (requestData: transactionsRequest, {rejectWithValue}) => {
    try {
        const response  = await baseRequestAxios.get<transactionsResponse>("/api/contract/transaction", {
          params: requestData,
        })
        if (response.status === 200) {
            return response.data
        }
    } catch (errors){
        return  rejectWithValue(errors)
    }
  }
)
