import { useEffect} from 'react';
import { AppStateType } from '../../../core/redux/store';
import { useSelector, useDispatch } from 'react-redux';
import styles from './NumberOfPartners.module.scss';
import { PartnersThunk } from '../../../modules/dashboard/partners/partnersThunk';

import { ReactSVG } from 'react-svg';
import partnersIcon from '../icons/partners.svg';

export const NumberOfPartners = (): JSX.Element => {
  const { partners } = useSelector((state: AppStateType) => state.partners);
  const dispatch = useDispatch()

  useEffect(() => {
     dispatch(PartnersThunk());
 }, [dispatch])

  return (
    <div className={styles.wrapper}>
      <div className={styles.NumberOfPartners}>
        <div className={styles.iconBox}>
          <ReactSVG className={styles.partnersIcon} src={partnersIcon} />
        </div>
        <div className={styles.sum}>
          <div className={styles.partnersPhoto}>
          {partners.map(partner => (
            <div className={styles.partnerPhoto} key={partner.id}>
              <img src={`https://stage-admin.mango.trade${partner.image}`} alt="NewsImage" />
            </div>
          ))}
          </div>
          {partners.length}
        </div>
        <div className={styles.text}>
          Общее количество партнеров
        </div>
      </div>
    </div>
  )
}
