import styles from './ContractItem.module.scss';
import {ReactSVG} from 'react-svg';
import DNCCoin from '../../../dashboard/icons/DNCBalance.svg';
import RZMCoin from '../../../dashboard/icons/RZMBalance.svg';
import rejectContractIcon from '../../icons/XBox.svg';
import {RejectContract} from '../../rejectContract/rejectContract';
import { useState } from 'react';

interface Props {
    item: Contract
}

export const ContractItem = ({item}: Props) => {
  const { id, amount, dailyIncome, rzmDailyIncome, dateCreate } = item;
  const [isRejectBlockShow, setIsRejectBlockShow] = useState(false);

  const toggleRejectBlock = () => {
    setIsRejectBlockShow(!isRejectBlockShow);
  }

  return (
    <>
      <li className={styles.item}>
        <div className={styles.firstPart}>
          <div className={styles.index}>
            № {id}
          </div>
          <div className={styles.MobileDate}>
            {dateCreate}
            <span className={styles.mobileText}>Дата</span>
          </div>
          <div className={styles.amount}>
            {amount}
            <ReactSVG className={styles.coin} src={DNCCoin} />
          </div>
        </div>
        <div className={styles.secondPart}>
          <div className={styles.MobileAmount}>
            <div>
              {amount}
              <ReactSVG className={styles.coin} src={DNCCoin} />
            </div>
            <span className={styles.mobileText}>Сумма</span>
          </div>
          <div className={styles.dnc}>
            <div>
              {dailyIncome.toFixed(4)}
              <ReactSVG className={styles.coin} src={DNCCoin} />
            </div>
            <span className={styles.mobileText}>DNC</span>
          </div>
          <div className={styles.rzm}>
            <div>
              {rzmDailyIncome.toFixed(4)}
              <ReactSVG className={styles.coin} src={RZMCoin} />
            </div>
            <span className={styles.mobileText}>RZM</span>
          </div>
          <div className={styles.date}>
            {dateCreate}
          </div>
          <div className={styles.rejectContract}>
            <div className={styles.rejectBox} onClick={() => toggleRejectBlock()}>
              <ReactSVG className={styles.rejectContractIcon} src={rejectContractIcon} />
            </div>
          </div>
        </div>
      </li>
      {isRejectBlockShow && (
        <div className={styles.rejectBlock}>
          <RejectContract id={id} toggleRejectBlock={toggleRejectBlock} />
        </div>
      )}
    </>
  )
}
