import {createAsyncThunk} from '@reduxjs/toolkit';
import {baseRequestAxios} from '../../../api/baseRequest';
import {StructureBonuses} from "../../../api/type/structure";

export const structureBonuses = createAsyncThunk(
    'structureBonuses',
    async (_, {rejectWithValue}) => {
        try {
            const response = await baseRequestAxios.get<StructureBonuses>("/api/structure/bonuses")
            if (response.status === 200) {
                return response.data
            }
        } catch (errors) {
            return rejectWithValue(errors)
        }
    }
)
