import {useForm} from "react-hook-form";
import {useDispatch, useSelector} from "react-redux";
import {ErrorsMessage} from "../../../../ui/errorsMessage/errorsMessage";
import {Input} from "../../../../ui/inputs/input/input";
import stranger from "../../../authorization/authorization.module.css";
import {ButtonEnter} from "../../../../ui/buttons/buttonEnter/buttonEnter";
import {RegistrationFormData1} from "../../../../api/type/registration";
import {onChangeForm1} from "../../../../modules/registration/registrationSlice";
import {AppStateType} from "../../../../core/redux/store";
import style from '../../registration.module.css';
import {PropsForm} from "./registrationForms";
import {FormHeader} from "./formHeader";

export const RegistrationForm1 = ({toggleActiveStep}: PropsForm): JSX.Element => {
    const {error, firstName, lastName, referral} = useSelector((state: AppStateType) => state.registration)
    const dispatch = useDispatch();

    const {handleSubmit, control, formState: {errors}} = useForm<RegistrationFormData1>({
        defaultValues: {
            firstName: firstName,
            lastName: lastName,
            referral: referral,
        },
        mode: 'onChange',
    });

    const onSubmit = (data: RegistrationFormData1) => {
        dispatch(onChangeForm1(data))
        toggleActiveStep(1)
    }

    return (
        <>
            <FormHeader title={'Регистрация'} description={'Как вас зовут и кто вас пригласил?'} />

            <form onSubmit={handleSubmit(onSubmit)}>
                <div className={`${stranger.formItem}`}>
                    {error ? (<ErrorsMessage textMessage={'Глобальная ошибка'}/>) :
                        (<label htmlFor="firstName">Имя</label>)}

                    <Input<RegistrationFormData1> defaultValue={firstName} control={control}
                                                  name={'firstName'} rules={{required: true}}/>
                    {errors.firstName &&
                    (<ErrorsMessage textMessage={'Имя введено некорректно'}/>)}

                </div>

                <div className={`${stranger.formItem}`}>
                    {error ? (<ErrorsMessage textMessage={'Глобальная ошибка'}/>) :
                        (<label htmlFor="lastName">Фамилия</label>)}
                    <Input defaultValue={lastName} control={control} name='lastName' rules={{required: true}}/>

                    {errors.lastName &&
                    (<ErrorsMessage textMessage={'Поле Фамилия не заполнено'}/>)}
                </div>

                <div className={`${stranger.formItem} ${style.referral}`}>
                    {error ? (<ErrorsMessage textMessage={'Этого партнера не существует'}/>) :
                        (<label htmlFor="referral">ID Партнера</label>)}
                    <Input defaultValue={referral} control={control} name={'referral'} rules={{required: true}}/>
                    {errors.referral &&
                    (<ErrorsMessage textMessage={'имя введено некорректно'}/>)}
                </div>

                <div className={`${stranger.footerForm} ${style.footerForm}`}>
                    <ButtonEnter value={'Продолжить'}/>
                </div>
            </form>
        </>
    )
}