import {createSlice} from '@reduxjs/toolkit';
import {structureLines} from './structureLinesThunk';
import {StructureLines} from "../../../api/type/structure";

const structureLinesSlice = createSlice({
    name: 'structureLines',
    initialState: {
        lines: {} as StructureLines,
        error: false,
        loading: false
    },
    reducers: {},
    extraReducers: (builder) => {
        //StructureLines
        builder.addCase(structureLines.pending, (state) => {
            state.loading = true;
        });
        builder.addCase(structureLines.fulfilled, (state, action) => {
            if (action.payload) {
                state.lines = action.payload
                state.loading = false
            }
        });
        builder.addCase(structureLines.rejected, (state) => {
            state.loading = false;
            state.error = true;
        });
    },

});

export const structureLinesReducer = structureLinesSlice.reducer;