import {useForm} from "react-hook-form";
import {Input} from "../../../../ui/inputs/input/input"
import {ErrorsMessage} from "../../../../ui/errorsMessage/errorsMessage";
import {ButtonEnter} from "../../../../ui/buttons/buttonEnter/buttonEnter";

interface Props {
    onSubmitHandle: (data: {email: string}) => void,
    error: boolean,
}

export const ForgotPasswordForm = ({onSubmitHandle, error}: Props): JSX.Element => {

    const {handleSubmit, control, formState: {errors}} = useForm<{ email: string }>({
        defaultValues: {
            email: '',
        },
        mode: 'onChange',
    });

    const onSubmit = (data: { email: string }) => {
        onSubmitHandle(data)
    }

    return (
        <form onSubmit={handleSubmit(onSubmit)}>
            {error ? <ErrorsMessage textMessage={''}/> :
                <label htmlFor="email">E-mail</label>}
            <Input control={control} name='email' rules={{required: true}}/>
            {errors.email &&
            <ErrorsMessage textMessage={''}/>}
            <ButtonEnter onClick={() => {
                console.log(34)
            }}> Восстанвить пароль</ButtonEnter>
        </form>
    )
}