import {createSlice} from '@reduxjs/toolkit';
import {sendSmsCode} from './sendSmsCodeThunk';

const sendSmsCodeSlice = createSlice({
    name: 'sendSmsCode',
    initialState: {
        id: 0,
        codeSms: '',
        loading: false,
        error: false,
    },
    reducers: {},
    extraReducers: (builder) => {
        builder.addCase(sendSmsCode.pending, (state) => {
            state.loading = true;
        });
        builder.addCase(sendSmsCode.fulfilled, (state, action) => {
            if (action.payload) {
                state.loading = false;
                state.id = action.payload.id;
                state.error = false;
            }
        });
        builder.addCase(sendSmsCode.rejected, (state, action) => {
            state.loading = false;
            state.error = true;
            // @ts-ignore
            state.errorMessage = action.payload.response.data.message
        });
    }
})

export const sendSmsCodeReducer = sendSmsCodeSlice.reducer;