import {ReactSVG} from "react-svg";
import {useDispatch, useSelector} from "react-redux";
import iconLongArrow from '../../../icon/iconLongArrow.svg'
import {
    toggleIsChangedData,
    updateNotificationsEmailForm,
    updateNotificationsTgForm
} from "../../../../../modules/user/getUserData/getUserDataSlice";
import {NotificationsFormEmailProps, NotificationsFormTgProps} from "../../../../../api/type/user";
import {AppStateType} from "../../../../../core/redux/store";
import {FormEmail} from "./forms/formEmail";
import {FormTelegram} from "./forms/formTelegram";
import style from './notifications.module.css'

export const Notifications = (): JSX.Element => {
    const dispatch = useDispatch()
    const {isChangedData} = useSelector((state: AppStateType) => state.user)

    const onChangeEmailForm = (data: NotificationsFormEmailProps) => {
        dispatch(updateNotificationsEmailForm(data))
        dispatch(toggleIsChangedData({value: !isChangedData}))
    }

    const onChangeTelegramForm = (data: NotificationsFormTgProps) => {
        dispatch(updateNotificationsTgForm(data))
        dispatch(toggleIsChangedData({value: !isChangedData}))
    }

    return (
        <div className={style.notifications}>
            <div className={style.notifications_socialNetwork}>
                <div className={style.blockItem}>
                    <h4>Email</h4>
                    <FormEmail onSubmitHandle={onChangeEmailForm}/>
                </div>
                <div className={style.blockItem}>
                    <h4>Telegram</h4>
                    <FormTelegram onSubmitHandle={onChangeTelegramForm}/>
                </div>
            </div>
            <div className={style.notifications_telegramBot}>
                <h4>Как подключить телеграм бот?</h4>
                <div className={style.instruction}>
                    <div className={style.instruction_item}>
                        <div className={style.instruction_item_text}>
                            <span className={style.step}>1.</span>
                            <span className={style.title}>Перейти в бот</span>
                        </div>
                        <ReactSVG className={style.iconLongArrow} src={iconLongArrow}/>
                    </div>

                    <div className={style.instruction_item}>
                        <div className={style.instruction_item_text}>
                            <span className={style.step}>2.</span>
                            <span className={style.title}>Нажать кнопку старт</span>
                        </div>
                        <ReactSVG className={style.iconLongArrow} src={iconLongArrow}/>
                    </div>

                    <div className={style.instruction_item}>
                        <div className={style.instruction_item_text}>
                            <span className={style.step}>3.</span>
                            <span className={style.title}>Подключить бот</span>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}