import {createSlice} from '@reduxjs/toolkit';
import {structureBonusesTransactions} from './bonusesTransactionsThunk';
import {BonusesTransactions} from "../../../api/type/structure";

const bonusesTransactionsSlice = createSlice({
    name: 'structureBonuses',
    initialState: {
        bonusesTransactions: {} as BonusesTransactions,
        error: false,
        loading: false
    },
    reducers: {},
    extraReducers: (builder) => {
        builder.addCase(structureBonusesTransactions.pending, (state) => {
            state.loading = true;
        });
        builder.addCase(structureBonusesTransactions.fulfilled, (state, action) => {
            if (action.payload) {
                state.bonusesTransactions = action.payload
                state.loading = false
            }
        });
        builder.addCase(structureBonusesTransactions.rejected, (state) => {
            state.loading = false;
            state.error = true;
        });
    },

});

export const bonusesTransactionsReducer = bonusesTransactionsSlice.reducer;