import { createSlice } from '@reduxjs/toolkit';
import { articlesThunk } from './articlesThunk';

const articlesSlice = createSlice({
  name: 'articles',
  initialState: {
    isShow: false,
    articles: [] as any,
    loading: false,
    error: null,
  },
  reducers: {
    addArticles(state, action) {
      state.articles = action.payload;
    }
  },
  extraReducers: (builder) => {
    builder.addCase(articlesThunk.pending, (state) => {
      state.loading = true;
      state.error = null;
    });
    builder.addCase(articlesThunk.fulfilled, (state, action) => {
      if (action.payload) {
      state.articles = action.payload.articles;
      }
    });
    builder.addCase(articlesThunk.rejected, (state) => {
      state.loading = false
    });
  },
});

export const {addArticles} = articlesSlice.actions;
export const articlesReducer = articlesSlice.reducer;